<template>
	<div>
		<div id="class" class="m_c">
		</div>
		<div class="m_class">
			<p class="title">我的班级</p>
			<div class="c_l">
				<div class="c_n" v-for="(c, i) in c_list" :key='i' @click='_goto(c.id, c.name)'>
					<p>{{c.name}}</p>
					<p>({{c.memberCount}}人)</p>
				</div>
			</div>
		</div>
		<div class="content">
			<div class="panel" v-for="r in p_list">
				<router-link :to="{name: l.href}" v-for="l in r" :key='r.id'>
					<img :src="l.src" class="icon_0"></img>
					<span v-if="l.flag" class="icon_1"></span>
					<p>{{l.title}}</p>
				</router-link>
			</div>
		</div>
		<foot :show="0"></foot>
	</div>
</template>

<script>
	import f from './footer.vue'
	export default {
		data(){
			return {
				// 班级列表
				c_list: [],

				// 功能模块列表
				p_list: [
					[{id:1, href: 'fbrw', title: '发布任务', src: '../../static/images/pic_fbrw.png', flag: false},
					{id:2, href: 'ckbg',title: '查看报告', src: '../../static/images/pic_ckbg.png', flag: false},
					{id:3, href: 'pydhg',title: '批阅读后感', src: '../../static/images/pic_pydhg.png', flag: false},
					{id:5, href: 'ydjy',title: '阅读教研', src: '../../static/images/pic_ydjy.png', flag: false}],
					[{id:7, href: 'ydzx',title: '阅读资讯', src: '../../static/images/pic_ydzx.png', flag: false}]
				],
				t_app: false
			}
		},
		components: {
			'foot': f // 添加底部导航组件
		},
		created: function(){
			var type = this.util.getAppScene();
			if(type == 'app'){//app应用端调用
				this.t_app=true;
			}else{
				this.t_app=false;
			}

			var self=this;
			// --------------请求班级列表begin--------------
			// 先判断全局变量中是否有数据，若无则向后台请求数据
			if(!this.util.getSessionData('clist')){
				this.util.request(
				{
					url: self.globalurl.host+self.globalurl.url.bjlb,
					data: {"loginAccount": self.util.getSessionData("userName")},
					dataType: 'json',
					type: 'POST'
				}, function(data){
						if(data.statusCode==200&&data.classes.length!=0){
							self.util.setSessionData('cid', data.classes[0].id);
							self.util.setSessionData('clist', data.classes);
							self.c_list=data.classes;
						}
				}, self)
			}else{
				this.c_list=this.util.getSessionData('clist');
			}
			// --------------请求班级列表end--------------
		},
		methods: {
			_goto: function(id, name){
				var path='/ckbj?cid='+id+'&name='+name;
				this.$router.push(path);
			}
		},
		beforeRouteLeave(to, from, next){
			if(!this.t_app){
				if(to.path=="/sc"){
					window.history.replaceState({title: 'sc', url: '#/sc'}, "sc", "#/sc");
				}else if(to.path=="/main"){
					window.history.replaceState({title: 'main', url: '#/main'}, "main", "#/main");
				}
			}
			next();
		}
	}
</script>

<style scoped>
	/*我的班级*/
	.m_c{
		position: relative;
		width: 100%;
		height: 3.62rem;
		background-image: url('../../static/images/pic_01.png');
		background-size: 100% 100%;
	}
	.m_class{
		width: 100%;
		background-color: #fff;
		border-radius: 0.1rem;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.title{
		font-size: 0.35rem;
		width: 100%;
		padding: 0.30rem 0.29rem;
		border-bottom: 0.01rem solid #e6e6e6;
	}
	.c_l{
		/*width: 100%;*/
		margin: 0.30rem 0.3rem;
		/*margin: */
		display: box;
		display: -webkit-box;
		overflow: auto;
		background-image: url('../../static/images/pic_more.png');
		background-size: 0.16rem 0.28rem;
		background-repeat: no-repeat;
		background-position: 6.6rem center;
	}
	.c_list{
		display: box;
		display: -webkit-box;
		width: 100%;
		height: 0.73rem;
		overflow: auto;
		/*background-image: url('../../static/images/pic_more.png');
		background-size: 0.16rem 0.28rem;
		background-repeat: no-repeat;
		background-position: 6.6rem center;*/
	}
	.c_n{
		display: block;
		width: 3.5rem;
		height: 0.73rem;
		text-align: center;
		color: #333333;
		/*overflow: hidden;*/
	}
	.c_n p{
		/*float: left;*/
		line-height: 0.73rem;
		display: inline-block;
		line-height: 0.73rem;
		font-size: 0.34rem;
		color: #333333;
	}
	.c_n p:nth-child(2){
		color: #40cc8b;
	}
	.c_c{
		float: right;
		width: 1.81rem;
		height: 0.73rem;
		background-color: #40cc8b;
		border: 0px;
		border-radius: 0.1rem;
	}
	.c_c a{
		display: block;
		width: 100%;
		height: 100%;
		text-align: center;
		line-height: 0.73rem;
		color: #fff;
		font-size: 0.3rem;
	}
	.content{
		padding: 0px 0.3rem;
		margin-top: 0.19rem;
		margin-bottom: 1.11rem;
		width: 100%;
		background-color: #fff;
		border-top: 0.01rem solid #e7e7e7;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.panel{
		display: flex;
		display: -webkit-flex;
		flex-flow: row;
		width: 100%;
		height: 2rem;
	}
	.panel:nth-child(2n){
		border-top: 0.01rem solid #e7e7e7;
	}
	.panel a{
		position: relative;
		padding: 0.48rem 0.13rem;
		width: 1.725rem;
		display: block;
	}
	.icon_1{
		position: absolute;
		top: 0.2rem;
		right: 0.2rem;
		display: inline-block;
		width: 0.17rem;
		height: 0.17rem;
		background-image: url('../../static/images/pic_sign.png');
		background-size: 100% 100%;
	}
	.icon_0{
		margin: 0 auto;
		display: block;
		width: 0.57rem;
		height: 0.57rem;
	}
	.panel a p{
		margin-top: 0.22rem;
		font-size: 0.28rem;
		text-align: center;
		color: #1a1a1a;
	}

</style>


