<template>
	<div>
		<header>
			<a href="javascript:void(0)"  class="goback" @click="_goback" v-if='!t_app'></a>
			<router-link to='/fbrw/kh' class='goback' v-if='t_app'></router-link>
			<p>添加图书</p>
		</header>
		<div class="tjts">
			<p>已添加图书：{{sc_list.length}}本</p>
			<a href='javascript:void(0)' @click='_goto'>添加图书</a>
		</div>
		<footer v-show='flag'>
			<router-link to='/fbrw/yl'>预览</router-link>
			<a href='javascript:void(0)' @click='_send'>发布任务</a>
		</footer>
		<tips :msg='message' v-show='t_flag'></tips>
		<div class="ydjy_list" v-for="(sc, i) in sc_list">
			<router-link :to="{name: 'sc_xq'}">
				<div class="ydjy_l">
					<img :src="sc.pic" class="ydjy_lPic" style=" position: relative; ">
				</div>
			</router-link>
			<div class="ydjy_r">
				<router-link :to="{name: 'sc_xq'}" ><div class="hdzq_titil" >{{sc.name}}</div></router-link>
				<div class="author">{{sc.author}}<span>著</span></div>
				<div style="display: flex; justify-content: left; align-items: center;">
					<span class="ts_label">故事</span>
					<span class="ts_label">科普</span>
					<span class="ts_label">美国</span>
				</div>
				<ul class="record" >
					<li>{{sc.view}}阅读</li>
					<li>{{sc.love}}点赞</li>
				</ul>
				<div class="btn">
					<a href="javascript:void(0)" @click='_del(i)'></a>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	var timer=null;
	import tips from './msgtips.vue'
	export default {
		data(){
			return {
				// 阅读教研 列表
				sc_list: [],
				// 提示框
				message: '',
				t_flag: false,
				t_app: false
			}
		},
		created: function(){
			var type = this.util.getAppScene();
			if(type == 'app'){//app应用端调用
				this.t_app=true;
			}else{
				this.t_app=false;
			}
			if(this.util.getSessionData('rwinfo').b_list){
				this.sc_list=this.util.getSessionData('rwinfo').b_list;
			}
		},
		computed: {
			flag: function(){
				if(this.sc_list.length!=0){
					return true;
				}else{
					return false;
				}
			}
		},
		methods: {
			_del: function(i){
				// 同时删除sessionData和列表数据
				this.sc_list.splice(i, 1);
				var rwInfo=this.util.getSessionData('rwinfo');
				rwInfo.b_list.splice(i, 1);
				rwInfo.bstr.splice(i, 1);
				this.util.setSessionData('rwinfo', rwInfo);
			},
			// 发布任务
			_send: function(){
				var self=this;
				var rwInfo=this.util.getSessionData('rwinfo');
				this.util.request(
				{
					url: self.globalurl.host+self.globalurl.url.fbrw,
					data: {
						"planName": rwInfo.name,
						"beginDate": rwInfo.b_time,
						"endDate": rwInfo.e_time,
						"classes": rwInfo.bj,
						"access": rwInfo.khfs,
						"books": rwInfo.bstr.join(),
						"account": self.util.getSessionData("userName"),
						"reason": rwInfo.tjy,
						"voiceReason": '',
						"requirements": rwInfo.ydyq,
						"requirementsVoice": ''
					},
					dataType: 'json',
					type: 'POST'
				}, function(data){
						if(data.statusCode==500){
							clearTimeout(timer);
							timer=null;
							self.message='发送失败';
							self.t_flag=true;
							timer=setTimeout(function(){
								self.t_flag=false;
							}, 2000);
						}
						if(data.statusCode==200){
							clearTimeout(timer);
							timer=null;
							self.message='发送成功';
							self.t_flag=true;
							timer=setTimeout(function(){
								self.t_flag=false;
								self.util.removeSessionData('rwinfo');
								self.$router.push('/');
							}, 2000);
						}
				}, self)
			},
			_goto: function(){
				if(this.sc_list.length>=6){
					clearTimeout(timer);
					timer=null;
					this.message='超过添加数量';
					this.t_flag=true;
					timer=setTimeout(function(){
						self.t_flag=false;
					}, 2000);
				}else{
					this.$router.push('/sc');
				}
			},
			_goback: function(){
				window.history.back();
			}
		},
		components: {
			"tips": tips
		},
		beforeRouteEnter(to, from, next){
			next(function(vm){
				vm.util.removeSessionData('tj_yl');
			})
		}
	}
</script>

<style scoped>
	.tjts{
		display: flex;
		flex-flow: row;
		justify-content: space-between;
		width: 100%;
		padding: 0.3rem;
		margin-top: 0.88rem;
		background-color: #fff;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.tjts p{
		position: relative;
		padding-left: 0.25rem;
		font-size: 0.35rem;
		line-height: 1rem;
	}
	.tjts p:after{
		content: '';
		position: absolute;
		top: 50%;
		left: 0px;
		width: 0.1rem;
		height: 0.35rem;
		margin-top: -0.175rem;
		border-left: 0.1rem solid #40cc8b;
	}
	.tjts a{
		display: block;
		padding-left: 0.5rem;
		width: 3rem;
		height: 1rem;
		border-radius: 0.1rem;
		background-color: #40cc8b;
		color: #fff;
		font-size: 0.35rem;
		text-align: center;
		line-height: 1rem;
		background-image: url('../../static/images/ico_24fff.png');
		background-size: 0.3rem 0.3rem;
		background-repeat: no-repeat;
		background-position: 0.5rem center;
	}
	.ydjy_list{
		width: 100%;  
		height: 2.9rem; 
		background-color: #fff; 
		position: relative;
		border-bottom: #ececec 0.01rem solid;
		overflow: auto;
	}
	.ydjy_list:last-child{
		margin-bottom: 1rem;
	}
	.ydjy_l{
		width: 1.9rem; 
		height:2.4rem; 
		margin: 0.25rem 0.4rem 0.18rem 0.32rem; 
		float: left; 
	}
	.ydjy_lPic{
		width: 1.9rem;
		height: 2.4rem; 
	}
	.ydjy_r{
		height: 100%; 
		width: 4.55rem; 
		float: left; 
	}
	.ico_mark{
		background-color: #edc900; 
		border-radius: 0.8rem; 
		vertical-align: middle; 
		padding: 0.04rem 0.15rem; 
		color: #fff; 
		font-size: 0.3rem; 
	}
	.play{
		position: absolute; 
		background-color: #000;  
		width: 1.91rem; 
		height:2.4rem;   
		top:0.25rem; 
		left: 0.31rem; 
		z-index: 88; 
		display: flex; 
		align-items: center; 
		justify-content: center; 
		filter:alpha(opacity=30);  /*支持 IE 浏览器*/
		-moz-opacity:0.30; /*支持 FireFox 浏览器*/
		opacity:0.30;  /*支持 Chrome, Opera, Safari 等浏览器*/	
	}
	.markName{
		width: 0.23rem; 
		height: 0.26rem; 
		padding-top: 0.03rem;
	}
	.record{
		width: 100%; 
		height: 0.4rem;
		font-size: 0.3rem; 
		display: flex; 
		flex-flow: row;
		color: #999; 
		font-family: 微软雅黑; 
		overflow: hidden;
	}
	.record li{
		flex: 1;
		height: 100%; 
		justify-content: left;
		align-items: center;
	}
	.ico_22{
		width: 0.6rem;
		height: 0.6rem;
		position:absolute; 
		top:1.2rem; 
		left:0.95rem; 
		z-index:99;	
	}
	.b_list{
		font-size: 0.3rem; 
		color: #666; 
		line-height: 0.55rem;
	}
	.record li{
		float: left;
		width: 30%;
		display: flex; 
		justify-content: left;
		align-items: center;
	}
	.pic_see{
		width: 0.33rem; 
		height: 0.22rem; 
	}
	.pic_praise{
		width: 0.28rem; 
		height: 0.23rem; 
	}
	.hdzq_titil{
		color:#333; 
		font-size:0.36rem; 
		line-height: 0.6rem;
		margin-top: 0.15rem;
		overflow:hidden; 
		text-overflow:ellipsis; 
		white-space:nowrap;
	}
	.ts_label{
		font-size: 0.26rem; 
		background-color:#cffae6; 
		color: #666; 
		padding: 0.04rem 0.15rem; 
		margin: 0.1rem; 
		margin-left: 0; 
		border-radius: 0.05rem;
	}
	.ydjy_state{
		 background-color: #f60; 
		 color:#fff; 
		 font-size: 0.22rem; 
		 position:absolute; 
		 top:0.4rem; 
		 padding: 0.08rem 0.15rem;
		 z-index: 89;
		 border-radius: 0 0.2rem 0.2rem 0;    
	}
	.author{
		font-size: 0.3rem; 
		color: #666;
		line-height: 0.4rem;
	}
	.btn{
		margin-top: 0.1rem;
		width: 100%;
		height: 0.5rem;
		display: flex;
		flex-flow: row;
		justify-content: flex-end;
	}
	.btn a{
		width: 0.5rem;
		height: 0.5rem;
		background-image: url('../../static/images/pic_del.png');
		background-size: 100% 100%;
	}
	footer{
		position: fixed;
		bottom: 0px;
		left: 0px;
		width: 100%;
		height: 1rem;
		padding: 0.1rem 0.3rem;
		background-color: #40cc8b;
		display: flex;
		flex-flow: row;
		justify-content: space-around;
		z-index: 999999999;
	}
	footer a{
		width: 3rem;
		margin: 0px 0.2rem;
		height: 0.8rem;
		border: 0.01rem solid #e7e7e7;
		color: #fff;
		border-radius: 0.1rem;
		text-align: center;
		line-height: 0.8rem;
		font-size: 0.35rem;
	}
</style>


