<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
  export default {
    data(){
      return {
      }
    },
    created: function(){
      var self=this;
      var type = "";//ajax处理方式
      var testObj = self.util.getTestObj();
      var isTest = testObj["isTest"];
      if(isTest){//测试用
        type = "GET";
      }else{//部署用
        type = "POST";
      }

      // 请求教师账号
      $.ajax({
        url: self.globalurl.host+self.globalurl.url.wxSession,
        data: {},
        dataType: 'json',
        type: type, 
        async: false,
        success: function(data){
          if(data.StatusCode==200){
            if(isTest){//测试用
              self.util.setSessionData("userName", data.data["userId"]);
              // self.util.setSessionData("userName", '80012273');
              self.util.setSessionData("schoolCode", "2144017149");//80000091用户的学校code
            }else{//部署用
              if(data.data && data.data != ''){
                  if(data.data["userName"]){
                    self.util.setSessionData("userName", data.data["userName"]);
                  }
                  if(data.data["schoolCode"]){
                    self.util.setSessionData("schoolCode", data.data["schoolCode"]);
                  }
                  if(data.data["userName"] == '' || data.data["schoolCode"] == ''){// 走APP调用
                    if(window.js && window.js.getSessionObj){//APP调用原生Android JS方法
                      var objStr = window.js.getSessionObj();
                      if(objStr != ''){
                        var obj = JSON.parse(objStr);
                        var userName = obj.userName;//用户账号
                        var schoolCode = obj.schoolCode;//学校代码
                        if(userName && userName != ''){
                          window.sessionStorage.setItem('userName', userName);
                        }
                        if(schoolCode && schoolCode != ''){
                          window.sessionStorage.setItem('schoolCode', schoolCode);
                        }
                      }
                    }
                  }
              }
            }
          self.util.token(self.util.getSessionData("userName"));
          }
        }
      })
      // 请求微信信息
      var paramUrl = window.location.href.split("#/")[0];
      $.ajax({
        type : "POST",
        dataType : "html",
        data : {paramUrl: paramUrl},
        url : self.globalurl.host+self.globalurl.url.jssdk,
        success : function (a) {
          self.util.setSessionData('rets',  eval("(" + a + ")"));
        }
      })
    }
  }
</script>

<style>
  html,body{
    background: #f3f3ef;
  }
  /*头部*/
  header{
    position: fixed;
    z-index: 999;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 0.88rem;
    background-color: #40cc8b;
    text-align: center;
  }
  header p{
    font-size: 0.40rem;
    line-height: 0.88rem;
    color: #fff;
  }
  .goback{
    position: absolute;
    top: 50%;
    left: 0.25rem;
    margin-top: -0.185rem;
    display: inline-block;
    width: 0.37rem;
    height: 0.37rem;
    background-image: url('../static/images/pic_fh.png');
    background-size: 100% 100%;
  }
</style>



