<template>
	<div>
		<header>
			<a href="javascript:void(0)"  class="goback" @click="_goto" v-if='!t_app'></a>
			<router-link to='/' class='goback' v-if='t_app'></router-link>
			<p>发布任务</p>
		</header>
		<div class="content">
			<div class="c_m">
				<p class="c_m_t">任务名称</p>
				<input type="text" name="" class="c_n" placeholder="填写任务名称" v-model='name'>
				<p class="c_m_t">任务时间</p>
				<div class="c_b">
					<input id='t_begin' type="text" name="" :data-lcalendar="cb_time" v-binit readonly v-model='b_time'>
					<p>设置开始时间：</p>
				</div>
				<div class="c_b">
					<input id='t_end' type="text" name="" :data-lcalendar="ce_time" v-einit readonly v-model='e_time'>
					<p>设置结束时间：</p>
				</div>
				<div class="c_py">
					<p  class="c_m_t">老师推荐语(选填)</p>
					<textarea placeholder="您的推荐能帮助学生更好地理解本次任务..." v-model='tjy'></textarea>
				</div>
				<div class="yy" v-show='y_flag'>
					<button v-ly></button>
					<p>点击按钮录音，再次点击结束</p>
				</div>
				<div class="cc_yy" v-show='yy_flag' @click='_play'>{{y_time}}s</div>
				<router-link to='/fbrw/kh' class='c_nt'>下一步</router-link>
				<p class="c_t">共3步</p>
				<div class="time" v-show='y_tflag'>
					<p>{{y_time}}<span>s</span></p>
				</div>
			</div>
		</div>
		<tips :msg='message' v-show='t_flag'></tips>
	</div>
</template>

<script>
	// 用于给自定义事件提供Vue实例
	var that=null;
	var timer=null;
	import tips from './msgtips.vue'
	export default {
		data(){
			return {
				demo: 0,
				// 任务名称
				name: '',
				// 开始时间
				b_time: '',
				// 结束时间
				e_time: '',
				// 老师推荐语
				tjy: '',
				// 提示框
				message: '',
				t_flag: false,
				// 初始化开始时间
				cb_time: '',
				// 初始化结束时间
				ce_time: '',
				// 语音
				y_flag: false,
				localId: '',
				y_time: 0,
				// 时间记录
				y_tflag: false,
				// 语音条
				yy_flag: false,
				// 语音播放状态
				// false为停止状态 true为播放状态
				y_state: false,
				t_app: false
			}
		},
		created: function(){
			var type = this.util.getAppScene();
			if(type == 'app'){//app应用端调用
				this.t_app=true;
			}else{
				this.t_app=false;
			}
			that=this;
			// 初始化时间范围
			this.cb_time=this.util.getDate(true, 0)+",2100-12-31";
			this.ce_time=this.util.getDate(false, 14)+",2100-12-31";
			// 初始化时间
			this.b_time=this.util.getDate(true, 0);
			this.e_time=this.util.getDate(false, 14);

			// 微信SDK
			var ret=this.util.getSessionData('rets');
			wx.config({
				debug: false,
				appId: ret.data.appID,
				timestamp: ret.data.timestamp,
				nonceStr: ret.data.nonceStr,
				signature: ret.data.signature,
				jsApiList: [
					'startRecord',
					'stopRecord',
					'playVoice',
					'pauseVoice',
					'stopVoice',
					'onVoicePlayEnd',
					'uploadVoice'
				]
			});
		},
		methods: {
			_sY: function(){
				if(this.y_flag){
					this.y_flag=false;
				}else{
					this.y_flag=true;
				}
			},
			_play: function(event){
				var self=this;
				if(this.y_state){
					wx.stopVoice({
					    localId: self.localId // 需要停止的音频的本地ID，由stopRecord接口获得
					});
					this.y_state=false;
					event.target.style.backgroundImage='url("../../static/images/xs_pic_yy.png")';
				}else{
					wx.playVoice({
						localId: self.localId // 需要播放的音频的本地ID，由stopRecord接口获得
					});
					this.y_state=true;
					event.target.style.backgroundImage='url("../../static/images/xs_pic_yy.gif")';
					wx.onVoicePlayEnd({
						success: function (res) {
							var localId = res.localId; // 返回音频的本地ID
							this.y_state=true;
							event.target.style.backgroundImage='url("../../static/images/xs_pic_yy.png")';
						}
					});
				}
			},
			_goto: function(){
				window.history.back();
			}
		},
		watch: {
			b_time: function(){
				this.ce_time=this.b_time+",2100-12-31";
			}
		},
		directives: {
			binit: {
				inserted: function(el){
					var calendar = new lCalendar();
					calendar.init({
					    'trigger': '#t_begin',
					    'type': 'date'
					});
				}
			},
			einit: {
				inserted: function(el){
					var calendar = new lCalendar();
					calendar.init({
					    'trigger': '#t_end',
					    'type': 'date'
					});
				}
			},
			ly: {
				inserted: function(el){
					// if(!that.y_tflag){
					// 	// el.addEventListener('touchstart', function(){
					// 	wx.startRecord();
					// 	// 显示计时器
					// 	that.y_tflag=true;
					// 	// 时间清零
					// 	that.y_time=0;
					// 	// 隐藏语音条
					// 	that.yy_flag=false;
					// 	timer=setInterval(function(){
					// 		that.y_time+=1;
					// 	}, 1000);
					// }else{
					// 	// });
					// 	// el.addEventListener('touchend', function(){
					// 	wx.stopRecord({
					// 		success: function(res){
					// 			that.localId=res.localId;
					// 			// 关闭计时器
					// 			that.y_tflag=false;
					// 			// 显示语音条
					// 			that.yy_flag=true;
					// 			// 关闭语音按钮
					// 			that.y_flag=false;
					// 			clearInterval(timer);
					// 			timer=null;
					// 			// 语音上传到微信服务器
					// 			wx.uploadVoice({
					// 				localId: res.localId, // 需要上传的音频的本地ID，由stopRecord接口获得
					// 				isShowProgressTips: 0, // 默认为1，显示进度提示
					// 				success: function (res) {
					// 					var serverId = res.serverId; // 返回音频的服务器端ID
					// 				}
					// 			});
					// 		}
					// 	});
					// }
					// });
				}
			}
		},
		// 导航加载钩子
		beforeRouteEnter(to, from, next){
			// 当从下一步返回到该页面时，恢复原有信息
			if(from.path=='/fbrw/kh'){
				next(function(vm){
					var rwInfo=vm.util.getSessionData("rwinfo");
					if(rwInfo){
						vm.name=rwInfo.name;
						vm.b_time=rwInfo.b_time;
						vm.e_time=rwInfo.e_time;
						vm.tjy=rwInfo.tjy;
					}
				});
			}else{
				next();
			}
		},
		// 导航离开钩子
		beforeRouteLeave(to, from, next){
			if(to.path=='/fbrw/kh'){
				if(this.name==''){
					var self=this;
					clearTimeout(timer);
					timer=null;
					this.message='请填写任务名称';
					this.t_flag=true;
					timer=setTimeout(function(){
						self.t_flag=false;
					}, 2000);
					next(false);
				}else{
					// 将数据保存在sessionStorage里
					var rwInfo=this.util.getSessionData("rwinfo");
					if(rwInfo){
						rwInfo.name=this.name;
						rwInfo.b_time=this.b_time;
						rwInfo.e_time=this.e_time;
						rwInfo.tjy=this.tjy;
						if(this.y_time!=0){
							rwInfo.y_time=this.y_time;
						}
						this.util.setSessionData('rwinfo', rwInfo);
					}else{
						var rwInfo={};
						rwInfo.name=this.name;
						rwInfo.b_time=this.b_time;
						rwInfo.e_time=this.e_time;
						rwInfo.tjy=this.tjy;
						if(this.y_time!=0){
							rwInfo.y_time=this.y_time;
						}
						this.util.setSessionData('rwinfo', rwInfo);
					}
					next();
				}
			}else{
				this.util.removeSessionData('rwinfo');
				next();
			}
		},
		components: {
			'tips': tips
		}
	}
</script>

<style scoped>
	.content{
		position: absolute;
		top: 0.88rem;
		bottom: 0px;
		width: 100%;
		background-color: #fff;
		padding: 0.3rem;
	}
	.c_m{
		width: 100%;
		height: 100%;
		overflow: auto;
	}
	.c_n{
		width: 100%;
		border: 0px;
		border-bottom: 0.01rem solid #e7e7e7;
		height: 1rem;
		font-size: 0.33rem;
		margin-bottom: 0.8rem;
	}
	.c_b{
		width: 100%;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.c_b p{
		height: 1rem;
		line-height: 1rem;
		font-size: 0.35rem;
		color: #888888;
	}
	.c_b input{
		float: right;
		width: 4rem;
		height: 1rem;
		font-size: 0.33rem;
		border: 0px;
		text-align: right;
	}
	.c_py{
		position: relative;
		margin-top: 0.8rem;
		width: 100%;
	}
	.c_m_t{
		border-left: 0.1rem solid #40cc8b;
		font-size: 0.35rem;
		padding-left: 0.1rem;
	}
	.c_py textarea{
		margin-top: 0.3rem;
		width: 100%;
		resize: none;
		border-radius: 0.1rem;
		height: 3rem;
		border: 0.01rem solid #e7e7e7;
		padding: 0.2rem;
		font-size: 0.33rem;
	}
	.c_py button{
		border: 0px;
		background-color: rgba(0,0,0,0);
		position: absolute;
		bottom: 0.2rem;
		right: 0.15rem;
		width: 0.45rem;
		height: 0.65rem;
		background-image: url('../../static/images/ico_02.png');
		background-size: 100% 100%;
	}
	.c_nt{
		display: block;
		width: 3rem;
		height: 1rem;
		background-color: #40cc8b;
		color: #fff;
		font-size: 0.35rem;
		text-align: center;
		line-height: 1rem;
		border-radius: 3rem;
		margin: 0 auto;
		margin-top: 0.3rem;
	}
	.c_t{
		margin-top: 0.2rem;
		text-align: center;
		font-size: 0.3rem;
		color: #777777;
	}
	.yy{
		margin-top: 0.2rem;
		width: 100%;
	}
	.yy button{
		display: block;
		width: 1.8rem;
		height: 1.8rem;
		margin: 0 auto;
		background-image: url('../../static/images/yy.png');
		background-color: rgba(0,0,0,0);
		background-size: 100% 100%;
		border: 0px;
	}
	.yy p{
		font-size: 0.33rem;
		color: #555555;
		text-align: center;
		margin-top: 0.2rem;
	}
	.cc_yy{
		/*float: left;*/
		margin-top: 0.3rem;
		width: 3rem;
		height: 0.8rem;
		border: 0.01rem solid #e8e8e8;
		background-color: #f2f2f2;
		border-radius: 0.1rem;
		font-size: 0.35rem;
		color: #40cc8b;
		line-height: 0.8rem;
		padding-left: 0.2rem;
		background-image: url('../../static/images/xs_pic_yy.png');
		background-size: 0.2rem 0.35rem;
		background-position: 2.5rem center;
		background-repeat: no-repeat;
	}
	.time{
		position: fixed;
		width: 3rem;
		height: 3rem;
		background-color: rgba(0,0,0,0.8);
		top: 50%;
		left: 50%;
		margin-top: -1.5rem;
		margin-left: -1.5rem;
		border-radius: 0.1rem;
	}
	.time p{
		width: 100%;
		text-align: center;
		line-height: 3rem;
		color: #fff;
		font-size: 1.4rem;
		font-weight: 900;
	}
</style>


