<template>
	<div style=" background-color: #fafafa; ">
		<header>
			<p>
				<img src="../../static/images/ico_look.png" class="look">
				<input type="text" name="" placeholder="搜索书籍名称" v-model='value' v-if='isbn==""'>
				<input type="text" name="" placeholder="搜索书籍isbn" v-model='isbn' v-if='isbn!=""'>
			</p>
			<router-link to='/sc'><span class="goback_r">取消</span></router-link>
		</header>
		<div class="main" v-show='flag'>
			<div class="hot_list">
				<img src="../../static/images/pic_hot.png" class="hot">
				<span>热门搜索</span>
			</div>
			<div class="hot_list" v-for='(h, i) in hotList' @click='_selectHot(h.word)'>
				<span class="hot orange">{{i+1}}</span>
				<span>{{h.word}}</span>
			</div>
		</div>
		<!-- 书详情内容 -->
		<div class="ydjy_list" v-for="sc in sc_list" :key='sc.id'>
			<router-link :to="'/sc/xq?id='+sc.id+'&type='+sc.type" >
			<div class="ydjy_l">
				<img :src="sc.pic" class="ydjy_lPic" style=" position: relative; ">
			</div>
	 		</router-link>
			<div class="ydjy_r">
				<router-link :to="'/sc/xq?id='+sc.id+'&type='+sc.type">
				<div class="hdzq_titil" >{{sc.name}}</div>
				<div class="author">{{sc.author}}<span>&nbsp;&nbsp;&nbsp;著</span></div>
				<div style="display: flex; justify-content: left; align-items: center;">
					<span class="ts_label">故事</span>
					<span class="ts_label">科普</span>
					<span class="ts_label">美国</span>
				</div>
				</router-link>
				<ul class="record">
					<li><img src="../../static/images/ico_see.png" class="pic_see" ></img>&nbsp;{{sc.view}}</li>
					<li @click='_dz(sc, $event)'><img :src="sc.isZan | dzImg" class="pic_praise"></img >&nbsp;{{sc.toast}}</li>
				</ul>
				<div class="btn">
					<a href="javascript:void(0)"></a>
					<a href="javascript:void(0)" @click='_addTs(sc)'></a>
				</div>
			</div>
		</div>
		<tips :msg='message' v-show='t_flag'></tips>
	</div>
</template>

<script>
	var timer=null;
	import tips from './msgtips.vue'
	export default {
		data(){
			return {
				// 热榜
				flag: true,
				// 提示框
				message: '',
				t_flag: false,
				// 阅读教研 列表
				sc_list: [],
				// 上拉加载
				sl_flag: false,
				// 页码
				page: 1,
				// 搜索框
				value: '',
				isbn: '',
				state: false,
				// 点赞
				dzImg: '../../static/images/ico_heart.png',
				hotList: [],
				nextpage: false
			}
		},
		filters: {
			dzImg: function(val){
				if(val==0){
					return '../../static/images/ico_heart.png';
				}else{
					return '../../static/images/ico_heart_show.png';
				}
			}
		},
		created: function(){
			var self=this;

			if(self.$route.query.isbn){
				self.value="";
				if(self.$route.query.isbn.split(',').length==2){
					self.isbn=self.$route.query.isbn.split(',')[1];
				}else{
					self.isbn=self.$route.query.isbn;
				}
			}else{
				self.isbn="";
			}

			// 热门关键字
			this.util.request(
			{
				url: self.globalurl.host+self.globalurl.url.tsrm,
				data: {},
				dataType: 'json',
				type: 'POST'
				}, function(data){
					if(data.status==200){
						self.hotList=data.result;
					}
			}, self)

			// 上拉加载
			window.addEventListener('scroll', function(){
				if(document.body.scrollTop+document.documentElement.clientHeight==document.body.scrollHeight){
					if(self.$route.path=='/sk/sk_search'){
						self.page+=1;
						self.sl_flag=true;
						self._load();
					}
				}
			})
		},
		methods: {
			_addTs: function(ts){
				var self=this;
				// 当前有数据保存在DOM存储里(从发布任务页面跳过来)
				if(this.util.getSessionData('rwinfo')){
					var rwInfo=this.util.getSessionData('rwinfo');
					if(rwInfo.b_list&&rwInfo.bstr){
						// 重复则什么都不做
						for(let i=0;i<rwInfo.b_list.length;i++){
							if(rwInfo.b_list[i].id==ts.id){
								clearTimeout(timer);
								timer=null;
								this.message='已添加';
								this.t_flag=true;
								timer=setTimeout(function(){
									self.t_flag=false;
								}, 2000);
								return false;
							}
						}
						rwInfo.b_list.push(ts);
						rwInfo.bstr.push(ts.id+"+"+ts.bookType);
					}else{
						rwInfo.b_list=[];
						rwInfo.bstr=[];
						rwInfo.b_list.push(ts);
						rwInfo.bstr.push(ts.id+"+"+ts.bookType);
					}
					this.util.setSessionData('rwinfo', rwInfo);
					this._goto();
				}else{
					// 当前没有数据保存在DOM存储里
					var rwInfo={};
					rwInfo.b_list=[];
					rwInfo.bstr=[];
					rwInfo.b_list.push(ts);
					rwInfo.bstr.push(ts.id+"+"+ts.bookType);
					this.util.setSessionData('rwinfo', rwInfo);
					this.$router.push("/fbrw");
				}
			},
			_goto: function(){
				// 判断页面是从添加图书跳转还是预览跳转
				if(this.$route.query.path){
					this.$router.push("/fbrw/ts");
				}else{
					this.$router.push("/fbrw/yl");
				}
			},
			// 点赞
			_dz: function(sc, event){
				var self=this;
				if(sc.isZan==0){
					sc.isZan+=1;
					sc.toast+=1;
					this.util.request(
					{
						url: self.globalurl.host+self.globalurl.url.tsdz,
						data: {"bookId": sc.id, "bookType": sc.type, "loginAccount":self.util.getSessionData('userName')},
						dataType: 'json',
						type: 'POST'
					}, function(data){
					}, self)
				}
			},
			_selectHot: function(val){
				this.value=val;
			},
			_load: function(){
				this.state=false;
				var self=this;
				if(!this.sl_flag){
					this.sc_list=[];
					this.page=1;
				}
				clearTimeout(timer);
				timer=null;
				this.message='加载中';
				this.t_flag=true;
				// --------------请求图书列表begin--------------
				this.util.request(
				{
					url: self.globalurl.host+self.globalurl.url.tsss,
					data: {"keywords": self.value, "isbn": self.isbn, "schoolCode": self.util.getSessionData('schoolCode'), "page": self.page, "pageSize": 10, 'loginAccount': self.util.getSessionData('userName')},
					dataType: 'json',
					type: 'POST'
				}, function(data){
					self.state=true;
					self.nextpage=data.nextPage;
					if(data.status==200&&!self.nextpage){
						clearTimeout(timer);
						timer=null;
						self.message='数据已到底';
						timer=setTimeout(function(){
							self.t_flag=false;
						}, 2000);
						self.sl_flag=false;
						// self.flag=true;
					}
					if(data.status==200&&data.result.length!=0){
						if(self.value!=false){
							for(let i=0;i<data.result.length;i++){
								self.sc_list.push(data.result[i]);
							}
							self.sl_flag=false;
							self.t_flag=false;
							self.flag=false;
						}else{
							self.t_flag=false;
							self.flag=true;
							self.sc_list=[];
						}
					}
					if(data.status==200&&data.result.length==0){
						clearTimeout(timer);
						timer=null;
						self.message='抱歉，没有找到相关书籍';
						timer=setTimeout(function(){
							self.t_flag=false;
						}, 2000);
						self.sl_flag=false;
						// self.flag=true;
					}
					if(data.status==500){
						clearTimeout(timer);
						timer=null;
						self.message='服务器开小差啦，请稍后再试';
						self.t_flag=true;
						timer=setTimeout(function(){
							self.t_flag=false;
						}, 2000);
						self.sl_flag=false;
						self.flag=true;
					}
				}, self)
				// --------------请求图书列表end--------------
			}
		},
		components: {
			'tips': tips
		},
		watch: {
			value: function(val){
				if(val!=false){
					this._load();
				}
			},
			isbn: function(val){
				var self=this;
				if(val!=''){
					this._load();
				}else{
					clearInterval(timer);
					timer=null;
					timer=setInterval(function(){
						self.flag=true;
						self.sc_list=[];
						if(self.state){
							clearInterval(timer);
							timer=null;
						}
					}, 10);
				}
			}
		}
	}
</script>

<style scoped>
	header{
		height: 0.98rem;
		background-color: #fff;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	header p{
		display: flex;
		align-items: center;
		font-size: 0.36rem;
		line-height: 0.88rem;
		width: 80%;
		background-color: #f0f0f0;
		border-radius: 0.08rem; 
		margin: 0.12rem 0.2rem;

	}
	header p input{
		width: 83%;
		height:0.7rem; 
		font-size: 0.32rem; 
		background-color: #f0f0f0;
		border:none;
	}
	.look{
		width: 0.44rem; 
		height: 0.44rem; 
		margin: 0 0.2rem; 
	}
	.main{
		position: relative; 
		top: 1.2rem; 
		background-color: #fff; 
		padding-bottom: 0.3rem;
	}
	.goback_r{
		position: absolute;
		top: 50%;
		right: 0.25rem;
		margin-top: -0.185rem;
		display: inline-block;
		width: 1rem;
		height: 0.37rem; 
		font-size: 0.32rem;
		color: #666;
	}
	
	.hot_list{
		display: flex; 
		justify-content: left; 
		align-items: center; 
		border-bottom: #e7e7e7 0.01rem solid;
		height: 0.88rem;
		font-size: 0.3rem;
		margin-left: 0.3rem;
		color: #666;
	}
	.hot{
		width: 0.4rem;
		height: 0.4rem;
		margin: 0 0.2rem 0 0;
	}
	.orange{
		color:#f60;
	}
	

	.ydjy_list{
		width: 100%;  
		background-color: #fff; 
		position: relative;
		top:0.85rem; 
		border-bottom: #ececec 0.01rem solid;
		overflow: hidden;
		padding: 0.2rem 0px;
	}
	.ydjy_list:last-child{
		margin-bottom: 1.9rem;
	}
	.ydjy_l{
		width: 1.9rem; 
		height:2.4rem; 
		margin: 0.25rem 0.4rem 0.18rem 0.32rem; 
		float: left; 
	}
	.ydjy_lPic{
		width: 1.9rem;
		height: 2.4rem; 
	}
	.ydjy_r{
		height: 100%; 
		width: 4.5rem; 
		float: left; 
	}
	.ico_mark{
		background-color: #edc900; 
		border-radius: 0.8rem; 
		vertical-align: middle; 
		padding: 0.04rem 0.15rem; 
		color: #fff; 
		font-size: 0.3rem; 
	}
	.play{
		position: absolute; 
		background-color: #000;  
		width: 1.91rem; 
		height:2.4rem;   
		top:0.25rem; 
		left: 0.31rem; 
		z-index: 88; 
		display: flex; 
		align-items: center; 
		justify-content: center; 
		filter:alpha(opacity=30);  /*支持 IE 浏览器*/
		-moz-opacity:0.30; /*支持 FireFox 浏览器*/
		opacity:0.30;  /*支持 Chrome, Opera, Safari 等浏览器*/	
	}
	.markName{
		width: 0.23rem; 
		height: 0.26rem; 
		padding-top: 0.03rem;
	}
	.record ,.record1{
		width: 100%; 
		height: 0.5rem;
		font-size: 0.3rem; 
		display: inline-block; 
		color: #999; 
		font-family: 微软雅黑; 
	}
	.record1 li{
		float: left;
		width: 30%; 
		height: 100%; 
		display: flex; 
		justify-content: left;
		align-items: center;
	}
	.ico_22{
		width: 0.6rem;
		height: 0.6rem;
		position:absolute; 
		top:1.2rem; 
		left:0.95rem; 
		z-index:99;	
	}
	.b_list{
		font-size: 0.3rem; 
		color: #666; 
		line-height: 0.55rem;
	}
	.record li{
		float: left;
		width: 30%;
		display: flex; 
		justify-content: left;
		align-items: center;
	}
	.pic_see{
		width: 0.33rem; 
		height: 0.22rem; 
	}
	.pic_praise{
		width: 0.28rem; 
		height: 0.23rem; 
	}
	.hdzq_titil{
		color:#333; 
		font-size:0.36rem; 
		line-height: 0.6rem;
		margin-top: 0.15rem;
		overflow:hidden; 
		text-overflow:ellipsis; 
		white-space:nowrap;
	}
	.ts_label{
		font-size: 0.26rem; 
		background-color:#c0f4dc; 
		color: #666; 
		padding: 0.04rem 0.15rem; 
		margin: 0.1rem; 
		margin-left: 0; 
		border-radius: 0.05rem;
	}
	.ydjy_state{
		 background-color: #f60; 
		 color:#fff; 
		 font-size: 0.22rem; 
		 position:absolute; 
		 top:0.4rem; 
		 padding: 0.08rem 0.15rem;
		 z-index: 89;
		 border-radius: 0 0.2rem 0.2rem 0;    
	}
	.author{
		font-size: 0.3rem; 
		color: #666;
		line-height: 0.4rem;
	}
	.btn{
		margin-top: 0.5rem;
		width: 100%;
		height: 0.5rem;
		display: flex;
		flex-flow: row;
		justify-content: flex-start;
	}
	.btn a{
		width: 0.5rem;
		height: 0.5rem;
	}
	.btn a:nth-child(1){
		margin-left: 0.3rem;
		background-image: url('../../static/images/ico_22.png');
		background-size: 100% 100%;
	}
	.btn a:nth-child(2){
		margin-left: 1rem;
		/*width: 0.4rem;*/
		background-image: url('../../static/images/ico_24.png');
		background-size: 100% 100%;
	}
	/*.btn a:nth-child(3){
		background-image: url('../../static/images/ico_24.png');
		background-size: 100% 100%;
	}*/
	.qx{
		position: fixed;
		bottom: 0px;
		left: 0px;
		z-index: 9999999999;
		width: 100%;
		padding: 0.1rem 0px;
		background-color: #40cc8b;
	}
	.qx a{
		display: block;
		width: 3rem;
		height: 0.8rem;
		border: 0.01rem solid #222222;
		background-color: #fff;
		font-size: 0.33rem;
		line-height: 0.8rem;
		text-align: center;
		margin: 0 auto;
		border-radius: 0.1rem;
		color: #111111;
	}
</style>


