					<div class="ydry_ztlist">{{teacher.TEA_NAME}}|{{teacher.SCHOOL_NAME}}</div>
				</div>
			</div>
			<div  class="ydrw_lump">
				<div class="fontsz">
					<span class="title_img" >&nbsp;</span>任务时间：{{result.beginDate}}&nbsp;~&nbsp;{{result.endDate}}
				</div>
			</div>
			<div class="ydrw_lump" v-show='result.reason!=""'>
				<div class="fontsz"><span class="title_img" >&nbsp;</span>老师推荐语</div>
				<div class="lstj">
