						<span>{{result.reason}}</span>
					</div>

				</div>
			</div>
			<div class="ydrw_lump">
					<div class="fontsz"><span class="title_img" >&nbsp;</span>考核心方式： {{result.access | khfs}}</div>
			</div>
			<div class="ydrw_lump" v-show='result.requirements!=""'>
				<div class="fontsz" ><span class="title_img" >&nbsp;</span>阅读要求</div>
				<div class="ydrw_lump_comt ">
					<span>{{result.requirements}}</span>
				</div>
