			</div>
		</div>

		<div style=" height: 0.98rem; "></div>

		<!-- 消息提示框 -->
		<msgtips :msg='msgtips' v-show='t_flag'></msgtips>
	</div>
</template>

<script>
	var timer=null;
	import tips from './tips.vue'
	import msgtips from './msgtips.vue'
	export default {
		data(){
			return {
				// 删除提示语
				message: '是否删除该本图书',
				// 删除提示框
				tip: false,
				// 消息提示框
				msgtips: '',
				t_flag: false,
				result: {},
				teacher: {},
				length: 0,
				t_app: false
			}
		},
		created: function(){
			var type = this.util.getAppScene();
			if(type == 'app'){//app应用端调用
				this.t_app=true;
			}else{
				this.t_app=false;
			}
			var self=this;
			this.util.request(
			{
				url: self.globalurl.host+self.globalurl.url.rwyl,
				data: {"planId": self.$route.query.id},
				dataType: 'json',
				type: 'POST'
			}, function(data){
				if(data.statusCode==200&&data.result!=null){
					self.result=data.result;
					self.teacher=data.result.teacher;
					self.length=data.result.books.length;
					self.t_flag=false;
					self.sl_flag=false;
				}
				if(data.statusCode==404){
					clearTimeout(timer);
					timer=null;
					self.message='暂无数据';
					timer=setTimeout(function(){
						self.t_flag=false;
					}, 2000);
					self.sl_flag=false;
				}
				if(data.status==500){
					clearTimeout(timer);
					timer=null;
					self.message='服务器开小差啦，请稍后再试';
					self.t_flag=true;
					timer=setTimeout(function(){
						self.t_flag=false;
					}, 2000);
					self.sl_flag=false;
				}
			}, self)
		},
		components: {
			'tips': tips,
			// 消息提示框
			'msgtips': msgtips
		},
		filters: {
			khfs: function(val){
				if(val=='1'){
					return '评测'
				}else if(val=='2'){
					return '写读后感'
				}else if(val=='1,2'){
					return '评测、写读后感'
				}else if(val=='2,1'){
					return '写读后感、评测'
				}
			}
		},
		methods: {
			_goto: function(){
				window.history.back();
			}
		}
	}
</script>

