<template>
	<div>
		<header>
			<a href="javascript:void(0)"  class="goback" @click="_goto" v-if='!t_app'></a>
			<router-link to='/ckbg' class='goback' v-if='t_app'></router-link>
			<p>{{this.$route.query.bname}}</p>
		</header>
		<div class="footer">
			<router-link :to='"/ckbg/gk?id="+this.$route.query.id+"&bid="+this.$route.query.bid+"&bname="+this.$route.query.bname' class="gk"><p>概括</p>
			</router-link>
			<div class="xq">
				<p>详情</p>
			</div>
		</div>
		<div class="options" style="left: 0px;" v-show='o_flag'>
			<p :class='{t_check: t_c===""}' @click='_tchoose("")'>全部</p>
			<p>小王子</p>
		</div>
		<div class="options" style="right: 0px;" v-show='o_flag1'>
			<p :class='{t_check: state===0}' @click='_schoose(0)'>已提交</p>
			<p :class='{t_check: state===1}' @click='_schoose(1)'>未提交</p>
			<p :class='{t_check: state===2}' @click='_schoose(2)'>补做</p>
		</div>
		<div class="title">
			<p>姓名</p>
			<p>正确率</p>
			<p>读后感(分)</p>
			<p>能力</p>
		</div>
		<div class="info" v-for='(si, i) in studentInfo' :key='i'>
			<p class="name">{{si.stuname}}</p>
			<p class="data">{{si.percent}}</p>
			<p class="grade">{{si.readscore}}</p>
			<div class="g_list">
				<p v-for='(a, i) in si.ability' :key='i'>{{a.name}}<span>{{a.ability}}</span></p>
			</div>
		</div>
		<tips :msg='message' v-show='t_flag'></tips>
	</div>
</template>

<script>
	var timer=null;
	import tips from './msgtips.vue'
	export default {
		data(){
			return {
				    code: "1",
				    // 列表数据
				    studentInfo: [],
				    // 页码
				    page: 1,
				    // 提示框
				    message: '加载中',
				    t_flag: false,
				    // 上拉加载
				    sl_flag: false,
				    // 条件选择框
				    o_flag: false,
				    o_flag1: false,
				    // 选择图书
				    t_c: '',
				    // 状态
				    state: 0,
				    t_app: false
			}
		},
		created: function(){
			var type = this.util.getAppScene();
			if(type == 'app'){//app应用端调用
				this.t_app=true;
			}else{
				this.t_app=false;
			}
			var self=this;
			this._load();
			// 上拉加载
			window.addEventListener('scroll', function(){
				if(document.body.scrollTop+document.documentElement.clientHeight==document.body.scrollHeight){
					if(self.$route.path=='/ckbg/xq'){
						self.page+=1;
						self.sl_flag=true;
						self._load();
					}
				}
			})
		},
		methods: {
			// 加载数据
			_load: function(){
				var self=this;
				this.t_flag=true;
				this.util.request(
				{
					url: self.globalurl.host+self.globalurl.url.bgxq,
					data: {"classId":this.$route.query.bid,"bookId": '',"planId": this.$route.query.id,"token":"11fasf", "page": self.page, "pageSize": 10},
					dataType: 'json',
					type: 'POST'
				}, function(data){
						if(data.statusCode==200&&data.infos.length!=0){
							for(let i=0;i<data.infos.length;i++){
								self.studentInfo.push(data.infos[i]);
								self.t_flag=false;
							}
							self.sl_flag=false;
						}
						if(data.statusCode==404){
							clearTimeout(timer);
							timer=null;
							self.message='数据已到底';
							timer=setTimeout(function(){
								self.t_flag=false;
							}, 2000);
							self.sl_flag=false;
						}
						if(data.status==500){
							clearTimeout(timer);
							timer=null;
							self.message='服务器开小差啦，请稍后再试';
							timer=setTimeout(function(){
								self.t_flag=false;
							}, 2000);
							self.sl_flag=false;
						}
				}, self)
			},
			// 展示/隐藏查询条件
			_choose: function(flag){
				if(flag=='ts'){
					if(this.o_flag){
						this.o_flag=false;
					}else{
						this.o_flag=true;
					}
				}else{
					if(this.o_flag1){
						this.o_flag1=false;
					}else{
						this.o_flag1=true;
					}
				}
			},
			// 选择图书
			_tchoose: function(flag){
				this.t_c=flag;
				this.o_flag=false;
			},
			// 选择状态
			_schoose: function(flag){
				this.state=flag;
				this.o_flag1=false;
			},
			_goto: function(){
				window.history.back();
			}
		},
		components: {
			"tips": tips
		}
	}
</script>

<style scoped>
	header{
		background-color: #fff;
	}
	header p{
		line-height: 0.88rem;
		color: #40cc8b;
	}
	.s_bg{
		padding: 0px 0.36rem;
		text-align: center;
		font-size: 0.36rem;
		color: #4d4d4d;
		width: 35%;
		height: 100%;
		border: 0px;
		background-color: rgba(255,255,255,0);
		background-image: url('../../static/images/ico_07.png');
		background-position: center right;
		background-repeat: no-repeat;
		background-size: 0.22rem 0.11rem;
	}
	.s_bg option{
		color: #4d4d4d;
		text-align: center;
	}
	.goback{
		background-image: url('../../static/images/pic_fh01.png');
	}
	.title{
		width: 100%;
		border-bottom: 0.01rem solid #e7e7e7;
		background-color: #fff;
		display: flex;
		flex-flow: row;
		padding: 0.2rem 0px;
	}
	.title p{
		flex: 1;
		color: #808080;
		font-size: 0.32rem;
		text-align: center;
	}
	.title p:nth-child(4){
		flex: 1.5;
	}
	.info:nth-child(4){
		margin-top: 1.56rem;
	}
	.info{
		display: flex;
		flex-flow: row;
		padding: 0.3rem 0px;
		border-bottom: 0.01rem solid #e7e7e7;
		background-color: #fff;
	}
	.info:last-child{
		margin-bottom: 1.1rem;
	}
	.select{
		width: 100%;
		height: 1rem;
		background-color: #fff;
		background-image: url('../../static/images/ico_17.png');
		background-size: 0.35rem 0.1rem;
		background-repeat: no-repeat;
		background-position: 4.3rem center;
		border-top: 0.01rem solid #e7e7e7;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.select p{
		width: 100%;
		height: 100%;
		line-height: 1rem;
		font-size: 0.35rem;
		color: #222222;
		text-align: center;
		padding-right: 0.5rem;
	}
	.condition{
		width: 100%;
		display: flex;
		flex-flow: row;
		background-color: #fff;
		border-top: 0.01rem solid #e7e7e7;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.condition p{
		flex: 1;
		padding-left: 0.3rem;
		height: 0.8rem;
		line-height: 0.8rem;
		color: #777777;
		font-size: 0.33rem;
		background-image: url('../../static/images/ico_17.png');
		background-size: 0.3rem 0.15rem;
		background-repeat: no-repeat;
		background-position: 3.3rem center;
		position: relative;
	}
	.condition p:nth-child(1):after{
		content: '';
		position: absolute;
		top: 50%;
		right: 0px;
		width: 0.02rem;
		height: 0.4rem;
		margin-top: -0.2rem;
		border-right: 0.02rem solid #e7e7e7;
	}
	.options{
		width: 50%;
		border: 0.01rem solid #e7e7e7;
		background-color: #fff;
		position: absolute;
		top: 2.82rem;
		/*left: 0px;*/
	}
	.options p{
		width: 100%;
		height: 0.7rem;
		line-height: 0.7rem;
		font-size: 0.33rem;
		color: #777777;
		padding-left: 0.3rem;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.options p.t_check{
		background-color: #40cc8b;
		color: #fff;
	}
	.options p:last-child{
		border: 0px;
	}
	.info p{
		flex: 1;
		font-size: 0.33rem;
		text-align: center;
	}
	.name{
		color: #333333;
	}
	.data{
		color: #40cc8b;
	}
	.grade{
		color: #f66c6c;
	}
	.g_list{
		flex: 1.5;
	}
	.g_list p{
		font-size: 0.3rem;
		text-align: left;
		color: #b7b7b7;
		margin-bottom: 0.2rem;
	}
	.g_list p:last-child{
		margin-bottom: 0px;
	}
	.g_list p span{
		margin-left: 0.27rem;
		color: #808080;
	}
	.g_list p:first-child span{
		margin-left: 0.01rem;
	}
	.footer{
		background-color: #fff;
		display: flex;
		flex-flow: row;
		width: 100%;
		height: 1.11rem;
		margin-top: 0.88rem;
		border-top: 0.01rem solid #e7e7e7;
	}
	.xq{
		position: relative;
		flex: 1;
		padding: 0.33rem 0px;
	}
	.xq:after{
		content: '';
		position: absolute;
		left: 0px;
		bottom: 0px;
		width: 100%;
		height: 0.1rem;
		border-bottom: 0.1rem solid #40cc8b;
	}
	.xq p{
		color: #40cc8b;
		border-left: 0.01rem solid #e6e6e6;
		font-size: 0.4rem;
		width: 100%;
		text-align: center;
	}
	.gk{
		flex: 1;
		color: #808080;
		font-size: 0.4rem;
	}
	.gk p{
		height: 1.1rem;
		text-align: center;
		line-height: 1.1rem;
	}
</style>


