<template>
	<div>
		<header>
			<a href="javascript:void(0)"  class="goback" @click="_goto" v-if='!t_app'></a>
			<router-link to='/' class='goback' v-if='t_app'></router-link>
			<p>我的读后感</p>
		</header>
		<div class="main">
			<div class="title">
				<p>{{title}}</p>
			</div>
			<div class="content">
				<p>{{content}}</p>
				<img :src='i' style='width: 100%;margin-top: 0.2rem;' v-for='i in imgList'>
				<div class="c_yy" @click='_play($event, "xs")' v-if='v_time'>{{v_time}}s</div>
				<audio :src="voice" id='ypx_xs' style='display: none'></audio>
				<p class="grade">{{score}}分</p>
			</div>
			<p class="tlt">老师评语</p>
			<div class="remark">
				<img :src="avatar">
				<p class="name">{{tname}}</p>
				<div class="cc_yy" @click='_play($event, "ls")' v-if='c_time'>{{c_time}}s</div>
				<audio :src="c_voice" id='ypx_ls' style='display: none'></audio>
				<p class="r_info">{{checkContent}}</p>
			</div>
		</div>
	</div>
</template>

<script>
	var timer=null;
	// var voice=null;
	import tips from './msgtips.vue'
	export default {
		data(){
			return {
				// 标题
				title: '',
				// 内容
				content: '',
				// 分数
				score: '',
				// 评语
				checkContent: '',
				// 老师名字
				tname: '',
				// 老师头像
				avatar: '',
				// 学生语音
				voice: null,
				// 老师语音
				c_voice: null,
				// 语音时长
				v_time: 0,
				// 提示框
				message: '',
				t_flag: false,
				// 评价语音时长
				c_time: 0,
				c_voice: null,
				imgList: [],
				t_app: false
			}
		},
		created: function(){
			var type = this.util.getAppScene();
			if(type == 'app'){//app应用端调用
				this.t_app=true;
			}else{
				this.t_app=false;
			}
			var self=this;
			this.message='加载中';
			this.flag=true;
			this.util.request(
			{
				url: self.globalurl.host+self.globalurl.url.dhxq,
				data: {
					'id': self.$route.query.id
				},
				dataType: 'json',
				type: 'POST'
			}, function(data){
					if(data.status==200&&data.result){
						self.voice=data.result.info.voice;
						self.c_voice=data.result.info.checkVoice;
						self.title=data.result.info.title;
						self.tname=data.result.teaName;
						self.avatar=data.result.avatar;
						self.content=data.result.info.content;
						self.score=data.result.info.score;
						self.imgList=data.result.info.image.split(",");
						self.checkContent=data.result.info.checkContent;
						if(data.result.info.duration){
							self.v_time=data.result.info.duration;
						}
						if(data.result.info.checkDuration){
							self.c_time=data.result.info.checkDuration;
						}
						self.flag=false;
					}
			}, self)
			// $.ajax({
			// 	url: self.globalurl.host+self.globalurl.url.dhxq,
			// 	data: {
			// 		'id': self.$route.query.id
			// 	},
			// 	dataType: 'json',
			// 	type: 'POST',
			// 	success: function(data){
			// 		if(data.status==200&&data.result){
			// 			self.voice=new Audio(data.result.info.voice);
			// 			self.c_voice=new Audio(data.result.info.checkVoice);
			// 			self.title=data.result.info.title;
			// 			self.tname=data.result.teaName;
			// 			self.avatar=data.result.avatar;
			// 			self.content=data.result.info.content;
			// 			self.score=data.result.info.score;
			// 			self.checkContent=data.result.info.checkContent;
			// 			if(data.result.info.duration){
			// 				self.v_time=data.result.info.duration;
			// 			}
			// 			if(data.result.info.checkDuration){
			// 				self.c_time=data.result.info.checkDuration;
			// 			}
			// 			self.flag=false;
			// 		}
			// 	}
			// })
		},
		components: {
			'tips': tips
		},
		methods: {
			// 播放语音
			_play: function(event, flag){
				var voice=document.getElementById("ypx_"+flag);
				// console.log(event.target.parentNode);
				// console.log(voice);
				if(voice.paused){
					// 停止播放后，点击将重新播放
					voice.load();
					voice.play();
					// console.log(voice.paused);
					event.target.style.backgroundImage='url(../../static/images/xs_pic_yy.gif)';
					
					var self=this;
					// 监听是否播放完毕
					clearInterval(timer);
					timer=null;
					timer=setInterval(function(){
						if(voice.ended){
							event.target.style.backgroundImage='url(../../static/images/xs_pic_yy.png)';
							clearInterval(timer);
							timer=null;
						}
					}, 1);
				}else{
					voice.pause();
					// console.log(voice.paused);
					event.target.style.backgroundImage='url(../../static/images/xs_pic_yy.png)';
				}
			},
			_goto: function(){
				// this.$router.go(-1);
				window.history.back();
			}
		}
	}
</script>

<style scoped>
	.py{
		position: absolute;
		top: 50%;
		right: 0.25rem;
		color: #fff;
		font-size: 0.30rem;
		margin-top: -0.15rem;
	}
	.main{
		position: absolute;
		top: 0.88rem;
		bottom: 0px;
		background-color: #fff;
		width: 100%;
	}
	.title{
		width: 100%;
		height: 0.9rem;
		background-color: #fff;
	}
	.title p{
		width: 100%;
		height: 100%;
		line-height: 0.9rem;
		text-align: center;
		font-size: 0.4rem;
		color: #333333;
		font-weight: 800;
		font-weight: 600;
	}
	.content{
		width: 100%;
		padding: 0px 0.35rem 0.4rem;
		background-color: #fff;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.content p{
		width: 100%;
		font-size: 0.35rem;
		color: #666666;
		line-height: 0.4rem;
	}
	p.grade{
		text-align: right;
		font-size: 0.4rem;
		color: #f66c6c;
		font-weight: 600;
		margin-top: 1.4rem;
	}
	.tlt{
		width: 100%;
		padding: 0.28rem 0.3rem;
		font-size: 0.35rem;
		color: #999999;
		background-color: #f3f3ef;
	}
	.remark{
		background-color: #fff;
		width: 100%;
		padding: 0.27rem 0.3rem;
	}
	.remark img{
		width: 0.8rem;
		height: 0.8rem;
		border-radius: 100%;
		float: left;
		margin-right: 0.2rem;
	}
	.name{
		margin-right: 0.25rem;
		float: left;
		font-size: 0.35rem;
		color: #40cc8b;
		height: 0.8rem;
		line-height: 0.8rem;
	}
	.remark button{
		float: left;
		background-color: #fff;
		width: 4rem;
		height: 0.8rem;
		border: 0.01rem solid #e7e7e7;
		border-radius: 0.1rem;
		line-height: 0.8rem;
		font-size: 0.33rem;
		color: #40cc8b;
		padding-left: 0.2rem;
		text-align: left;
		background-image: url('../../static/images/xs_pic_yy.png');
		background-size: 0.2rem 0.32rem;
		background-repeat: no-repeat;
		background-position: 3.5rem center;
	}
	.r_info{
		width: 100%;
		font-size: 0.34rem;
		color: #333333;
		margin-top: 1.2rem;
	}
	.c_yy{
		margin-top: 0.5rem;
		width: 3rem;
		height: 0.8rem;
		border: 0.01rem solid #e8e8e8;
		background-color: #f2f2f2;
		border-radius: 0.1rem;
		font-size: 0.35rem;
		color: #40cc8b;
		line-height: 0.8rem;
		padding-left: 0.2rem;
		background-image: url('../../static/images/xs_pic_yy.png');
		background-size: 0.2rem 0.35rem;
		background-position: 2.5rem center;
		background-repeat: no-repeat;
	}
	.cc_yy{
		float: left;
		width: 3rem;
		height: 0.8rem;
		border: 0.01rem solid #e8e8e8;
		background-color: #f2f2f2;
		border-radius: 0.1rem;
		font-size: 0.35rem;
		color: #40cc8b;
		line-height: 0.8rem;
		padding-left: 0.2rem;
		background-image: url('../../static/images/xs_pic_yy.png');
		background-size: 0.2rem 0.35rem;
		background-position: 2.5rem center;
		background-repeat: no-repeat;
	}
</style>


