<template>
	<div>
		<header>
			<a href="javascript:void(0)"  class="goback" @click="_goto" v-if='!t_app'></a>
			<router-link to='/main' class='goback' v-if='t_app'></router-link>
			<p>我的收藏</p>
		</header>
		<!-- <div class="search">
			<input type="text" name="" placeholder="搜索">
		</div> -->
		<div class="content" v-for='(l, i) in list'>
			<div class="c_m" v-touch>
				<router-link :to="'/sc/xq?id='+l.id+'&type='+l.type" >
					<div class="ydjy_l">
						<img :src="l.pic" class="ydjy_lPic" style=" position: relative; ">
					</div>
		 		</router-link>
				<div class="ydjy_r">
					<router-link :to="'/sc/xq?id='+l.id+'&type='+l.type">
						<div class="hdzq_titil" >{{l.name}}</div>
						<div class="author">{{l.author}}<span>&nbsp;&nbsp;&nbsp;著</span></div>
						<div class="author">出版社：{{l.press}}</div>
						<div class="author">isbn：{{l.isbn}}</span></div>
						<!-- <div style="display: flex; justify-content: left; align-items: center;">
								<span class="ts_label">{{l.bookType}}</span>
						</div> -->
					</router-link>
					<ul class="record">
						<li><img src="../../static/images/ico_see.png" class="pic_see" ></img>&nbsp;{{l.view}}</li>
						<li><img :src="l.isZan | dzImg" class="pic_praise"></img >&nbsp;{{l.toast}}</li>
					</ul>
				</div>
			</div>
			<button @click='del(i, l.collectId)' v-init></button>
		</div>
		<tips :msg='message' v-show='t_flag'></tips>
	</div>
</template>

<script>
	var timer=null;
	import tips from './msgtips.vue'
	export default {
		data(){
			return {
				// 提示框
				message: '',
				t_flag: false,
				list: [],
				// 页码
				page: 1,
				// 上拉加载
				sl_flag: false,
				nextpage: true,
				// 点赞
				dzImg: '../../static/images/ico_heart.png',
				t_app: false
			}
		},
		filters: {
			dzImg: function(val){
				if(val==0){
					return '../../static/images/ico_heart.png';
				}else{
					return '../../static/images/ico_heart_show.png';
				}
			}
		},
		methods: {
			del: function(i, collectId){
				var self=this;
				this.util.request(
				{
					url: self.globalurl.host+self.globalurl.url.scsc,
					data: {"collectId": collectId},
					dataType: 'json',
					type: 'POST'
				}, function(data){
					if(data.status==200){
						self.t_flag=true;
						clearTimeout(timer);
						self.message='删除成功';
						timer=setTimeout(function(){
							self.t_flag=false;
						}, 2000);
						// 删除
						self.list.splice(i, 1);
					}
					if(data.status==404){
						self.t_flag=true;
						clearTimeout(timer);
						self.message='删除失败';
						timer=setTimeout(function(){
							self.t_flag=false;
						}, 2000);
					}
					if(data.status==500){
						self.t_flag=true;
						clearTimeout(timer);
						self.message='服务器开小差啦，稍后再试';
						timer=setTimeout(function(){
							self.t_flag=false;
						}, 2000);
					}
				}, self)
			},
			_load: function(){
				var self=this;
				// 请求下载资源
				this.util.request(
				{
					url: self.globalurl.host+self.globalurl.url.wdsc,
					data: {"loginAccount":self.util.getSessionData("userName"), "page": self.page, "pageSize": 10},
					dataType: 'json',
					type: 'POST'
				}, function(data){
					if(data.status==200&&data.result!=null){
						for(let i=0;i<data.result.length;i++){
							self.list.push(data.result[i]);
						}
						self.t_flag=false;
						self.nextpage=data.nextPage;
					}
					if(data.status==200&&data.result.length==0){
						clearTimeout(timer);
						timer=null;
						self.message='暂无数据';
						self.t_flag=true;
						timer=setTimeout(function(){
							self.t_flag=false;
						}, 2000);
					}
					if(data.status==404){
						clearTimeout(timer);
						self.message='暂无数据';
						timer=setTimeout(function(){
							self.t_flag=false;
						}, 2000);
					}
					if(data.status==500){
						clearTimeout(timer);
						self.message='服务器开小差啦，稍后再试';
						timer=setTimeout(function(){
							self.t_flag=false;
						}, 2000);
					}
				}, self)
			},
			_goto: function(){
				// this.$router.go(-1);
				window.history.back();
			}
		},
		created: function(){
			var type = this.util.getAppScene();
			if(type == 'app'){//app应用端调用
				this.t_app=true;
			}else{
				this.t_app=false;
			}
			var self=this;
			this.message='加载中';
			this.t_flag=true;
			// 请求下载资源
			this._load();

			// 上拉加载
			window.addEventListener('scroll', function(){
				if(document.body.scrollTop+document.documentElement.clientHeight==document.body.scrollHeight){
					if(self.$route.path=='/main/sc'){
						if(self.nextpage){
							self.page+=1;
							self.sl_flag=true;
							self._load();
						}else{
							clearTimeout(timer);
							timer=null;
							self.message='数据已到底';
							self.t_flag=true;
							timer=setTimeout(function(){
								self.t_flag=false;
							}, 2000);
						}
					}
				}
			})
		},
		directives: {
			// 左拉删除
			touch: {
				inserted: function(el){
					// 手指初始位置
					var star=null;
					// 手指结束位置
					var end=null;
					// 偏移量
					var left=null;
					el.addEventListener('touchstart', function(event) {
							// 初始化偏移量
							var content=el.parentNode.parentNode.getElementsByTagName('div');
							for(let i=0;i<content.length;i++){
								if(content[i].className=='content'){
									content[i].getElementsByTagName('div')[0].style.left='0px';
								}
							}
						     // 如果这个元素的位置内只有一个手指的话
						    if (event.targetTouches.length == 1) {// 阻止浏览器默认事件，重要
						// 　　　　 event.preventDefault();
						        var touch = event.targetTouches[0];
						        // 把元素放在手指所在的位置
						    	star=touch.clientX;
						        }
					}, false);
					el.addEventListener('touchmove', function(event) {
						     // 如果这个元素的位置内只有一个手指的话
						    if (event.targetTouches.length == 1) {
						// 　　　event.preventDefault();// 阻止浏览器默认事件，重要 
						        var touch = event.targetTouches[0];
						        // 把元素放在手指所在的位置
						    	end=touch.clientX;
							left=end-star;
							if(left>0){
								el.style.left="0px";
							}else if(-left>100){
								el.style.left='-2rem';
							}else{
								el.style.left=left+'px';
						          }
						}
					}, false);
					el.addEventListener('touchend', function(event) {
						if(left>-100){
							el.style.left="0px";
						}
					}, false);
				}
			},
			// 删除后恢复原位
			init: {
				inserted: function(el){
					el.onclick=function(){
						// 初始化偏移量
						this.parentNode.getElementsByTagName('div')[0].style.left='0px';
					}
				}
			}
		},
		components: {
			'tips': tips
		}
	}
</script>

<style scoped>
	header{
		background-color: #fff;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.goback{
		background-image: url('../../static/images/pic_fh01.png');
	}
	header p{
		color: #333333;
		font-weight: 500;
	}
	.search{
		margin-top: 0.88rem;
		padding: 0.11rem 0.3rem;
		width: 100%;
	}
	.search input{
		display: block;
		border-radius: 0.1rem;
		width: 100%;
		height: 0.57rem;
		border: 0.01rem solid #e7e7e7;
		background-color: #fff;
		padding-left: 0.6rem;
		background-image: url('../../static/images/ico_look.png');
		background-size: 0.31rem 0.31rem;
		background-repeat: no-repeat;
		background-position: 0.15rem center;
	}
	.content{
		position: relative;
		margin-bottom: 0.1rem;
		border-top: 0.01rem solid #e7e7e7;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.content:nth-child(2){
		margin-top: 1rem;
	}
	.c_m{
		position: relative;
		left: 0px;
		padding: 0.3rem;
		background-color: #fff;
		z-index: 2;
		overflow: hidden;
	}
	.content button{
		border: 0px;
		background-color: #fff;
		width: 0.9rem;
		height: 0.9rem;
		margin-top: -0.45rem;
		position: absolute;
		top: 50%;
		right: 0.5rem;
		z-index: 1;
		border-radius: 100%;
		background-image: url('../../static/images/delete.png');
		background-size: 100% 100%;
		border-radius: 100%;
	}
	.ydjy_l{
		width: 1.9rem; 
		height:2.4rem; 
		margin-right: 0.3rem;
		float: left; 
	}
	.ydjy_lPic{
		width: 1.9rem;
		height: 2.4rem; 
	}
	.ydjy_r{
		height: 100%; 
		width: 4rem; 
		float: left; 
	}
	.ico_mark{
		background-color: #edc900; 
		border-radius: 0.8rem; 
		vertical-align: middle; 
		padding: 0.04rem 0.15rem; 
		color: #fff; 
		font-size: 0.3rem; 
	}
	.play{
		position: absolute; 
		background-color: #000;  
		width: 1.91rem; 
		height:2.4rem;   
		top:0.25rem; 
		left: 0.31rem; 
		z-index: 88; 
		display: flex; 
		align-items: center; 
		justify-content: center; 
		filter:alpha(opacity=30);  /*支持 IE 浏览器*/
		-moz-opacity:0.30; /*支持 FireFox 浏览器*/
		opacity:0.30;  /*支持 Chrome, Opera, Safari 等浏览器*/	
	}
	.markName{
		width: 0.23rem; 
		height: 0.26rem; 
		padding-top: 0.03rem;
	}
	.record{
		width: 100%; 
		height: 0.5rem;
		font-size: 0.3rem; 
		display: inline-block; 
		color: #999; 
		font-family: 微软雅黑; 
		margin-top: 0.1rem;
	}
	.ico_22{
		width: 0.6rem;
		height: 0.6rem;
		position:absolute; 
		top:1.2rem; 
		left:0.95rem; 
		z-index:99;	
	}
	.b_list{
		font-size: 0.3rem; 
		color: #666; 
		line-height: 0.55rem;
	}
	.record li{
		float: left;
		/*width: 30%;*/
		margin-right: 0.3rem;
		display: flex; 
		justify-content: left;
		align-items: center;
	}
	.pic_see{
		width: 0.33rem; 
		height: 0.22rem; 
	}
	.pic_praise{
		width: 0.28rem; 
		height: 0.23rem; 
	}
	.hdzq_titil{
		color:#333; 
		font-size:0.36rem; 
		line-height: 0.6rem;
		overflow:hidden; 
		text-overflow:ellipsis; 
		white-space:nowrap;
	}
	.ts_label{
		font-size: 0.26rem; 
		background-color:#c0f4dc; 
		color: #666; 
		padding: 0.04rem 0.15rem; 
		margin: 0.1rem; 
		margin-left: 0; 
		border-radius: 0.05rem;
	}
	.ydjy_state{
		 background-color: #f60; 
		 color:#fff; 
		 font-size: 0.22rem; 
		 position:absolute; 
		 top:0.4rem; 
		 padding: 0.08rem 0.15rem;
		 z-index: 89;
		 border-radius: 0 0.2rem 0.2rem 0;    
	}
	.author{
		font-size: 0.3rem; 
		color: #666;
		line-height: 0.4rem;
		margin-top: 0.05rem;
	}
</style>


