<template>
	<div>
		<header>
			<a href="javascript:void(0)"  class="goback" @click="_goto" v-if='!t_app'></a>
			<router-link to='/' class='goback' v-if='t_app'></router-link>
			<p>批阅读后感</p>
		</header>
		<div class="name">
			<p>{{xq.stuName}}&nbsp;{{xq.className}}</p>
		</div>
		<div class="title">
			<p>{{title}}</p>
		</div>
		<div class="content">
			<p>{{content}}</p>
			<img :src='i' style='width: 100%;margin-top: 0.2rem;' v-for='i in imgList'>
			<div class="c_yy" v-show='v_flag!="" || v_time!=0' @click='_play'>{{v_time}}s</div>
			<!-- <audio id='c_dhg' :src='voice' controls="controls" v-show='voice'></audio> -->
		</div>
		<div class="evaluate">
			<input id='pf' type="number" name="" placeholder="请打分，满分100分" v-model='grade'>
			<div class="mb">
				<p>写评语</p>
				<button @click='_show'>使用模板</button>
			</div>
			<textarea v-model='value' placeholder="请输入评语"></textarea>
			<!-- <button class="yy"></button> -->
		</div>
		<div class="footer">
			<button @click='_tj'>提交批阅</button>
		</div>
		<div class="pymb" v-show='m_flag'>
			<div class="p_c">
				<p @click='_checked'>非常好</p>
				<button @click='_close'>取消</button>
			</div>
		</div>
		<tips :msg='message' v-show='flag'></tips>
	</div>
</template>

<script>
	var timer=null;
	import tips from './msgtips.vue'
	export default {
		data(){
			return {
				// 提示框
				flag: false,
				message: '',
				// 分数
				grade: '',
				// 评语
				value: '',
				// 模板
				m_flag: false,
				// 读后感详情
				xq: {},
				ainfo: {},
				// 语音时间长度
				v_time: 0,
				// 语音
				voice: null,
				v_flag: '',
				// 标题
				title: '',
				// 内容
				content: '',
				imgList: [],
				t_app: false
			}
		},
		created: function(){
			var type = this.util.getAppScene();
			if(type == 'app'){//app应用端调用
				this.t_app=true;
			}else{
				this.t_app=false;
			}
			var self=this;
			this.message='加载中';
			this.flag=true;
			this.util.request(
			{
				url: self.globalurl.host+self.globalurl.url.dhxq,
				data: {
					'id': self.$route.query.id
				},
				dataType: 'json',
				type: 'POST'
			}, function(data){
				if(data.status==200&&data.result){
					self.xq=data.result;
					self.v_flag=data.result.info.voice;
					self.v_time=data.result.info.duration;
					self.voice=new Audio(data.result.info.voice);
					self.title=data.result.info.title;
					self.content=data.result.info.content;
					self.imgList=data.result.info.image.split(",");
					self.flag=false;
				}
			}, self)
		},
		components: {
			"tips": tips
		},
		methods: {
			_tj: function(){
				var self=this;
				if(this.grade==''){
					clearInterval(timer);
					timer=null;
					this.flag=true;
					this.message='请输入分数';
					timer=setTimeout(function(){
						self.flag=false;
					}, 2000);
					var top=document.getElementById('pf').offsetTop;
					this.util.scrollTo(top);
				}else if(this.grade>=0&&this.grade<=100&&this.value!=''){
					this.util.request(
					{
						url: self.globalurl.host+self.globalurl.url.dhpy,
						data: {
							'id': self.$route.query.id,
							'score': self.grade,
							'content': self.value,
							'voice': ''
						},
						dataType: 'json',
						type: 'POST'
					}, function(data){
						if(data.status==200){
							self.message='发布成功';
							self.flag=true;
							setTimeout(function(){
								self.flag=false;
								self.$router.go(-1);
							}, 2000);
						}
					}, self)
				}else{
					clearInterval(timer);
					timer=null;
					this.flag=true;
					this.message='请输入正确的分数';
					timer=setTimeout(function(){
						self.flag=false;
					}, 2000);
				}

				if(this.value==''){
					this.flag=true;
					this.message='请输入评语'
					setTimeout(function(){
						self.flag=false;
					}, 2000);
				}
			},
			_checked: function(event){
				this.value=event.target.innerHTML;
				this.m_flag=false;
			},
			_close: function(){
				this.m_flag=false;
			},
			_show: function(){
				this.m_flag=true;
			},
			// 播放语音
			_play: function(event){
				// this.v_time=this.voice.duration;
				if(this.voice.paused){
					// 停止播放后，点击将重新播放
					this.voice.load();
					this.voice.play();
					event.target.style.backgroundImage='url(../../static/images/xs_pic_yy.gif)';

					var self=this;
					// 监听是否播放完毕
					clearInterval(timer);
					timer=null;
					timer=setInterval(function(){
						if(self.voice.ended){
							event.target.style.backgroundImage='url(../../static/images/xs_pic_yy.png)';
							clearInterval(timer);
							timer=null;
						}
					}, 1);
				}else{
					this.voice.pause();
					event.target.style.backgroundImage='url(../../static/images/xs_pic_yy.png)';
				}
			},
			_goto: function(){
				window.history.back();
			}
		}
	}
</script>

<style scoped>
	#c_dhg{
		display: none;
	}
	.name{
		width: 100%;
		height: 1rem;
		margin-top: 0.88rem;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.name p{
		position: relative;
		font-size: 0.33rem;
		color: #333333;
		width: 100%;
		line-height: 1rem;
		padding: 0px 0.6rem;
	}
	.name p:before{
		content: '';
		position: absolute;
		top: 50%;
		left: 0.3rem;
		width: 0.1rem;
		height: 0.5rem;
		margin-top: -0.25rem;
		background-color: #40cc8b;
	}
	.title{
		width: 100%;
		height: 0.9rem;
		background-color: #fff;
	}
	.title p{
		width: 100%;
		height: 100%;
		line-height: 0.9rem;
		text-align: center;
		font-size: 0.4rem;
		color: #333333;
		font-weight: 800;
	}
	.content{
		width: 100%;
		padding: 0px 0.35rem 0.4rem;
		background-color: #fff;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.content p{
		width: 100%;
		font-size: 0.35rem;
		color: #666666;
		line-height: 0.4rem;
	}
	.c_yy{
		margin-top: 0.5rem;
		width: 3rem;
		height: 0.8rem;
		border: 0.01rem solid #e8e8e8;
		background-color: #f2f2f2;
		border-radius: 0.1rem;
		font-size: 0.35rem;
		color: #40cc8b;
		line-height: 0.8rem;
		padding-left: 0.2rem;
		background-image: url('../../static/images/xs_pic_yy.png');
		background-size: 0.2rem 0.35rem;
		background-position: 2.5rem center;
		background-repeat: no-repeat;
	}
	.evaluate{
		position: relative;
		width: 100%;
		padding: 0.46rem 0.3rem 0px;
		margin-bottom: 1.11rem;
	}
	.mb{
		width: 100%;
		display: flex;
		flex-flow: row;
		margin-top: 0.5rem;
	}
	.mb p{
		flex: 2;
		font-size: 0.33rem;
		line-height: 0.8rem;
		position: relative;
		padding-left: 0.3rem;
	}
	.mb p:before{
		content: '';
		position: absolute;
		top: 50%;
		left: 0px;
		width: 0.1rem;
		height: 0.5rem;
		margin-top: -0.25rem;
		background-color: #40cc8b;
	}
	.mb button{
		flex: 1;
		width: 100%;
		height: 0.8rem;
		border: 0px;
		border-radius: 0.1rem;
		background-color: #40cc8b;
		font-size: 0.33rem;
		color: #fff;
	}
	.evaluate input{
		width: 100%;
		padding: 0.26rem 0.24rem;
		font-size: 0.35rem;
		border: 0.01rem solid #e7e7e7;
		border-radius: 0.1rem;
	}
	.evaluate textarea{
		width: 100%;
		height: 2.21rem;
		border: 0.01rem solid #e7e7e7;
		padding: 0.24rem;
		font-size: 0.35rem;
		resize: none;
		margin-top: 0.3rem;
		border-radius: 0.1rem;
	}
	.footer{
		position: fixed;
		bottom: 0px;
		left: 0px;
		display: flex;
		flex-flow: row;
		justify-content: center;
		background-color: #40cc8b;
		width: 100%;
		border-top: 0.01rem solid #e7e7e7;
		padding: 0.1rem 0.3rem;
	}
	.footer button{
		border: 0.02rem solid #fff;
		background-color: #40cc8b;
		width: 2.5rem;
		height: 0.8rem;
		font-size: 0.33rem;
		color: #fff;
		border-radius: 0.1rem;
	}
	.yy{
		width: 0.45rem;
		height: 0.65rem;
		position: absolute;
		right: 0.4rem;
		bottom: 0.2rem;
		border: 0px;
		background-color: #fff;
		background-size: 100% 100%;
		background-image: url('../../static/images/ico_02.png');
	}
	.pymb{
		position: fixed;
		z-index: 999999999;
		top: 0px;
		bottom: 0px;
		width: 100%;
		background-color: rgba(0,0,0,0.7);
	}
	.p_c{
		position: absolute;
		bottom: 0px;
		left: 0px;
		width: 100%;
		background-color: #fff;
		padding: 0.3rem;
	}
	.p_c button{
		display: block;
		margin: 0 auto;
		margin-top: 0.2rem;
		border: 0px;
		width: 2.5rem;
		padding: 0.1rem 0px;
		border-radius: 0.1rem;
		background-color: #40cc8b;
		color: #fff;
		font-size: 0.33rem;
	}
	.p_c p{
		font-size: 0.33rem;
		border-bottom: 0.01rem solid #e7e7e7;
		line-height: 0.6rem;
	}
</style>


