<template>
	<div>
		<header>
			<router-link to='/ydjy' class="goback"></router-link>
			<p>预览</p>
			<a href="javascript:void(0)" class="fx"></a>
		</header>
		<div class="main">
			<p class="m_t">{{info.resourcesname}}</p>
			<div class="m_i">
				<img src="../../static/images/head_03.png">
				<div class="m_i_c">
					<p>{{info.view}}</p>
					<p>{{info.love}}</p>
					<p>{{info.comment}}</p>
					<p>{{info.download}}</p>
				</div>
				<p class="m_i_s">{{info.schoolName}}</p>
				<p class="m_i_n">{{info.author}}</p>
			</div>
		</div>
		<div class="content">
			<p>{{info.content}}</p>
		</div>
	</div>
</template>

<script>
	export default {
		data(){
			return {
				info: {}
			}
		},
		created: function(){
			// 请求data中resourcesId的值要改！！！
			var resourcesId=this.$route.query.id;
			// 请求文章信息
			var self=this;
			$.ajax({
				url: 'http://192.168.8.6:9999/app/wx/res/preview',
				data: {"token":"111","resourcesId":"1111"},
				dataType: 'json',
				type: 'POST',
				success: function(data){
					if(data.statusCode==200&&data.info!=null){
						self.info=data.info;
					}
				}
			})
		}
	}
</script>

<style scoped>
	/*头部*/
	header{
		position: fixed;
		z-index: 1;
		top: 0px;
		left: 0px;
		width: 100%;
		height: 0.88rem;
		background-color: #40cc8b;
		text-align: center;
	}
	header p{
		font-size: 0.4rem;
		line-height: 0.88rem;
		color: #fff;
	}
	.goback{
		position: absolute;
		top: 50%;
		left: 0.25rem;
		margin-top: -0.185rem;
		display: inline-block;
		width: 0.37rem;
		height: 0.37rem;
		background-image: url('../../static/images/pic_fh.png');
		background-size: 100% 100%;
	}
	.fx{
		position: absolute;
		display: block;
		width: 0.4rem;
		height: 0.4rem;
		background-image: url('../../static/images/xs_pic_upload.png');
		background-size: 100% 100%;
		right: 0.2rem;
		top: 0.25rem;
	}
	.main{
		width: 100%;
		background-color: #fff;
		margin-top: 0.88rem;
		padding: 0.2rem 0.3rem;
	}
	.m_t{
		font-size: 0.45rem;
		color: #171717;
		text-align: center;
	}
	.m_i{
		overflow: hidden;
		width: 100%;
		margin-top: 0.3rem;
	}
	.m_i img{
		float: left;
		width: 1rem;
		height: 1rem;
	}
	.m_i_s{
		padding-left: 1.3rem;
		margin-top: 0.1rem;
		font-size: 0.35rem;
		color: #59d79a;
	}
	.m_i_n{
		padding-left: 1.3rem;
		margin-top: 0.15rem;
		font-size: 0.38rem;
		color: #535353;
	}
	.m_i_c{
		float: right;
		overflow: hidden;
		margin-top: 0.67rem;
	}
	.m_i_c p{
		float: left;
		color: #626262;
		margin-left: 0.1rem;
		font-size: 0.3rem;
		padding-left: 0.35rem;
		background-repeat: no-repeat;
		background-position: 0px center;
		background-size: 0.3rem 0.25rem;
	}
	.m_i_c p:nth-child(1){
		background-image: url('../../static/images/ico_see.png');
	}
	.m_i_c p:nth-child(2){
		background-image: url('../../static/images/ico_heart.png');
	}
	.m_i_c p:nth-child(3){
		background-image: url('../../static/images/ico_comment.png');
	}
	.m_i_c p:nth-child(4){
		background-image: url('../../static/images/ico-down.png');
	}
	.content{
		padding: 0.2rem 0.3rem;
		background-color: #fff;
	}
	.content p{
		font-size: 0.35rem;
		line-height: 0.5rem;
	}
</style>


