<template>
	<div style=" background-color: #fafafa; ">
		<header>
			<a href="javascript:void(0)" @click="_goback" v-if='!t_app'><img src="../../static/images/xs_pic_fh.png" class="goback"></a>
			<router-link to='/sc' v-if='t_app'><img src="../../static/images/xs_pic_fh.png" class="goback"></router-link>
			<p>图书详情</p>
			<img :src="sc"  class="goback_r" @click='_sc'>
		</header>
		<!-- 书详情内容 -->
		<div class="b_name">{{b_info.bookName}}</div>
		<div class="book">
			<div>
				<div class="b_img">
					<img :src="b_info.bookPic">
				</div>
				<div class="b_info">
					<div class="b_list">作者：{{b_info.author}}/著 </div>
					<div class="b_list">出版社：{{b_info.press}}</div>
					<div class="b_list">ISBN: {{b_info.isbn}} </div>
					<div style="display: flex;	justify-content: left;align-items: center;">
						<span class="ts_label">{{b_info.bookType}}</span>
					</div>
				</div>
			</div>
		</div>
		<div class="jyzx">
			<span class="jyzx_title"><span class="title_img" >&nbsp;</span>专家导读</span>
		</div>
		<!-- 书简介 -->
		<div class="b_syn">
			<div style="position: relative">
				<img :src='b_info.guideImage' style='width: 100%;height: 5rem;' @click='_showBtn'>
				<button class="b_s_b" :style="playstate" @click='_play' v-show='btn_flag'></button>
				<video id="ts_v" :src="_isMp3(b_info.guideVoice)" v-if='!flag'  style=" width: 7.5rem; height: 3.8rem; background-color: #fff;position:relative;z-index: 999;display: none;" controls="controls"></video>
				<audio id="ts_v" :src="_isMp3(b_info.guideVoice)" v-if='flag'  style=" width: 7.5rem; height: 0.8rem; background-color: #fff;position:relative;z-index: 999;display: none;" controls="controls"></audio>
			</div>
			<div class="b_cnt">
				<p class="b_title"><span class="title_img" >&nbsp;</span>编辑推荐</p>
				<p v-html='b_info.editorIntro'></p>
			</div>
			<div class="b_cnt">
				<p class="b_title"><span class="title_img" >&nbsp;</span>内容简介</p>
				<p v-html='b_info.desc'></p>
			</div>
			<div class="b_cnt">
				<p class="b_title"><span class="title_img" >&nbsp;</span>作者简介</p>
				<p v-html='b_info.authorDesc'></p>
			</div>
		</div>
		<tips :msg='message' v-show='t_flag'></tips>
	</div>
</template>

<script>
	var timer=null;
	import tips from './msgtips.vue'
	export default {
		data(){
			return {
				// 图书信息
				b_info: {},
				// 提示框
				message: '',
				t_flag: false,
				// 判断是否为mp3
				flag: false,
				// 播放
				play: false,
				// 收藏
				isCollect: 0,
				btn_flag: true,
				t_app: false
			}
		},
		created: function(){
			var type = this.util.getAppScene();
			if(type == 'app'){//app应用端调用
				this.t_app=true;
			}else{
				this.t_app=false;
			}
			var self=this;
			// 过2秒后关闭播放按钮
			clearTimeout(timer);
			timer=null;
			timer=setTimeout(function(){
				self.btn_flag=false;
			}, 2000);
			this.message='加载中';
			this.t_flag=true;
			this.util.request(
			{
				url: self.globalurl.host+self.globalurl.url.tsxq+self.$route.query.id+'?type='+self.$route.query.type+'&loginAccount='+self.util.getSessionData('userName'),
				data: {},
				dataType: 'json',
				type: 'GET'
			}, function(data){
				if(data.status==200){
					self.b_info=data.result;
					self.isCollect=data.result.isCollect;
					self.t_flag=false;
				}
				if(data.status==500){
					self.message='服务器开小差啦，请稍后再试';
					clearTimeout(timer);
					timer=setTimeout(function(){
						self.t_flag=false;
					}, 3500);
				}
			}, self)
		},
		components: {
			'tips': tips
		},
		methods: {
			// 收藏
			_sc: function(){
				if(this.isCollect==0){
					this.isCollect=1;
					var self=this;
					this.util.request(
					{
						url: self.globalurl.host+self.globalurl.url.tssc,
						data: {'bookId': self.b_info.id, 'loginAccount': self.util.getSessionData('userName'), 'type': self.$route.query.type},
						dataType: 'json',
						type: 'POST'
					}, function(data){
						console.log(data);
						if(data.status==200||data.status==666){
							self.message='收藏成功';
							self.t_flag=true;
							clearTimeout(timer);
							timer=setTimeout(function(){
								self.t_flag=false;
							}, 2000);
						}
					}, self)
				}
			},
			_isMp3: function(val){
				if(/\.(?:mp3)$/.test(val)){
					// 音频
					this.flag=true;
				}else{
					// 视频
					this.flag=false;
				}
				return val;
			},
			_play: function(){
				var video=document.getElementById('ts_v');
				var self=this;
					if(!this.play){
					// 开始播放
					this.play=true;
					video.play();
					// 更新导读数量
					this.util.request(
					{
						url: self.globalurl.host+self.globalurl.url.tsdd,
						data: {'guideId': self.b_info.guideId},
						dataType: 'json',
						type: 'POST'
					}, function(data){
					}, self)
					clearInterval(timer);
					timer=null;
					video.style.display='block';
					timer=setInterval(function(){
						if(video.paused){
							video.style.display='none';
							// 暂停播放
							self.play=false;
							clearInterval(timer);
							timer=null;
						}
					}, 500);
				}else{
					// 暂停播放
					this.play=false;
					video.pause();
					video.style.display='none';
					clearInterval(timer);
					timer=null;
				}
			},
			_goback: function(){
				window.history.back();
			},
			// 显示播放按钮
			_showBtn: function(){
				if(this.btn_flag){
					this.btn_flag=false;
				}else{
					this.btn_flag=true;
				}
			}
		},
		computed: {
			playstate: function(){
				if(!this.play){
					return {backgroundImage: 'url("../../static/images/play.png")'}
				}else{
					return {backgroundImage: 'url("../../static/images/zt.png")'}
				}
			},
			sc: function(){
				if(this.isCollect==0){
					return "../../static/images/ico_heart01.png";
				}else{
					return "../../static/images/ico_heart_show.png";
				}
			}
		},
		watch: {
			btn_flag: function(val){
				var self=this;
				if(val){
					clearTimeout(timer);
					timer=null;
					setTimeout(function(){
						self.btn_flag=false;
					}, 2000);
				}
			}
		}
	}
</script>

<style scoped>
	header p{
		font-size: 0.36rem;
		line-height: 0.88rem;
		color: #fff;
	}
	.goback{
		position: absolute;
		top: 50%;
		left: 0.25rem;
		margin-top: -0.185rem;
		display: inline-block;
		width: 0.37rem;
		height: 0.37rem;
		background: no-repeat;
	}
	.goback_r{
		position: absolute;
		top: 50%;
		right: 0.25rem;
		margin-top: -0.185rem;
		display: inline-block;
		width: 0.37rem;
		height: 0.37rem;
	}
	.goback_r1{
		position: absolute;
		top: 50%;
		right: 1rem;
		margin-top: -0.185rem;
		display: inline-block;
		width: 0.37rem;
		height: 0.37rem;
	}

	.book{
		border-bottom: 0.01rem solid #f1f1ee;
		padding: 0.24rem 0.3rem;
		overflow: hidden;
		background-color: #fff;
	}
	.b_img{
		float: left;
		width: 1.8rem;
	}
	.b_img img{
		width: 1.8rem; 
		height:2.2rem; 
	}
	.b_info{
		float: left;
		width: 5rem;
		overflow: hidden;
		padding: 0rem 0px 0px 0.3rem;
	}
	.b_list{
		font-size: 0.3rem; 
		color: #666; 
		line-height: 0.55rem;
	}
	.b_name{
		margin-top: 0.88rem;
		font-size: 0.36rem;
		width: 7.5rem; 
		height: 0.88rem;
		background-color: #eafff8; 
		display: flex;
		align-items: center;
		justify-content: center; 
		color: #666; 
	}
	.b_syn{
		width: 100%;
		background-color: #fff;
	}
	.b_s_b{
		position: absolute;
		top: 1.5rem;
		left: 50%;
		margin-left: -1rem;
		width: 2rem;
		height: 2rem;
		border: 0px;
		background-color: rgba(0,0,0,0);
		background-size: 100% 100%;
	}
	.b_cnt{
		width: 100%;
		border-bottom: 0.01rem solid #e7e7e7;
		padding: 0.6rem 0.3rem;
	}
	.b_title{ 
		text-align: left;
		font-size: 0.36rem;
		color: #40cc8b;
	}
	.b_cnt p:nth-child(2){
		font-size: 0.27rem;
		color: #666666;
		margin-top: 0.2rem;
		line-height: 0.4rem;
	}

	.jyzx{
		width: 100%; 
		height: 0.88rem; 
		background-color: #fff;
		margin-top: 0.2rem;
		border-bottom: 0.01rem solid #e7e7e7;
		padding: 0 0.2rem ; 
		font-size: 0.36rem;
		display: flex;
		align-items: center; 
		justify-content: space-between;
	}
	.jyzx_title{
		color: #40cc8b;  
	}
	.title_img{
		background-color: #40cc8b; 
		margin-right: 0.1rem;}
	.ts_label{
		font-size: 0.26rem; 
		background-color:#ceffe5; 
		color: #666; 
		padding: 0.04rem 0.15rem; 
		margin: 0.1rem; 
		margin-left: 0; 
		border-radius: 0.05rem;


	}
	.record1{
		width: 100%; 
		height: 0.6rem;
		font-size: 0.3rem; 
		display: inline-block; 
		color: #999; 
		font-family: 微软雅黑; 
	}
	.record1 li{
		float: left;
		width: 20%; 
		height: 100%; 
		display: flex; 
		justify-content: left;
		align-items: center;
	}
	.ico_22{
		width: 0.4rem;
		height: 0.4rem;
	}
	.ico_23{
		
		width: 0.33rem;
		height: 0.4rem;
	}
	.ico_24{
		width: 0.37rem;
		height: 0.37rem;
	}
</style>


