<template>
	<div>
		<header>
			<a href="javascript:void(0)"  class="goback" @click="_goto" v-if='!t_app'></a>
			<router-link to='/ckbg' class='goback' v-if='t_app'></router-link>
			<p>{{this.$route.query.bname}}</p>
		</header>
		<div class="footer">
			<div class="gk">
				<p>概括</p>
			</div>
			<router-link :to='"/ckbg/xq?id="+this.$route.query.id+"&bid="+this.$route.query.bid+"&bname="+this.$route.query.bname' class="xq"><p>详情</p>
			</router-link>
		</div>
		<div class="i">
			<span class="icon"></span>
			<p class="title">平均完成率</p>
			<p class="data">{{finish.totalPercent}}%</p>
			<div class="i_bs">
				<p class="i_b" v-for='(fd, i) in finish.books' :key='i'>
					<span>{{fd.bookName}}</span>完成率：{{fd.finishPercent}}%</p>
			</div>
		</div>
		<div class="i" v-if='as_flag'>
			<span class="icon"></span>
			<p class="title">读后感平均分</p>
			<p class="data">{{avgScore.avgScore}}分</p>
			<div class="i_bs">
				<p class="i_b" v-for='(ad, i) in avgScore.books' :key='i'><span>{{ad.bookName}}</span>完成率：{{ad.avgScore}}%</p>
			</div>
		</div>
		<div class="i">
			<span class="icon"></span>
			<p class="title">闯关平均正确率</p>
			<p class="data">{{percent.totalPercent}}%</p>
			<div class="i_bs">
				<p class="i_b" v-for='(pd, i) in percent.books' :key='i'><span>{{pd.bookName}}</span>完成率：{{pd.rightPercent}}%</p>
			</div>
		</div>
		<div class="capacity">
			<div class="c_top">
				<span></span>
				<p>班级阅读能力</p>
			</div>
			<div class="c_exp">
				<div class="c_c" v-for='(d, i) in data' :key='i'>
					<probar :data='d.ability' :color='colors[i]'></probar>
					<p>{{d.name}}</p>
				</div>
			</div>
		</div>
		<tips :msg='message' v-show='t_flag'></tips>
	</div>
</template>

<script>
	var timer=null;
	import tips from './msgtips.vue'
	import probar from './pro_bar.vue'
	export default {
		data(){
			return {
				// 完成率
				finish: {},
				// 正确率
				percent: {},
				// 平均分
				as_flag: false,
				avgScore: {},
				// 进度条数据
				data: [{'ability': 0},{'ability': 0},{'ability': 0},{'ability': 0}],
				// 组件颜色
				colors: ['#ffc474', '#f66c6c', '#79a8ec', '#40cc8b'],

				// 具体某本书
				flag: false,

				// 图书选择列表
				l_flag: false,

				// 提示框
				message: '加载中',
				t_flag: false,
				t_app: false
			}
		},
		components: {
			'probar': probar,
			'tips': tips
		},
		created: function(){
			var type = this.util.getAppScene();
			if(type == 'app'){//app应用端调用
				this.t_app=true;
			}else{
				this.t_app=false;
			}
			var self=this;
			this.t_flag=true;
			// 加载数据
			this.util.request(
				{
					url: self.globalurl.host+self.globalurl.url.bggk,
					data: {"planId":this.$route.query.id,"classId": this.$route.query.bid,"bookId": '',"token":"11fasf"},
					dataType: 'json',
					type: 'POST'
				}, function(data){
					if(data.statusCode==200&&data.info!=null){
						self.percent=data.info.rightPercent;
						self.finish=data.info.finishPercent;
						if(data.info.feeling){
							self.avgScore=data.info.feeling;
						}
						self.data=data.info.ability;
						self.t_flag=false;
						if(data.info.access.indexOf(2)==-1){
							self.as_flag=false;
						}else{
							self.as_flag=true;
						}
					}
					if(data.status==500){
						clearTimeout(timer);
						timer=null;
						self.message='服务器开小差啦，请稍后再试';
						self.t_flag=true;
						timer=setTimeout(function(){
							self.t_flag=false;
						}, 2000);
					}
				},self)
		},
		methods: {
			_show: function(){
				if(this.l_flag){
					this.l_flag=false;
				}else{
					this.l_flag=true;
				}
			},
			_goto: function(){
				window.history.back();
			}
		}
	}
</script>

<style scoped>
	header{
		background-color: #fff;
	}
	header p{
		line-height: 0.88rem;
		color: #40cc8b;
	}
	.s_bg{
		padding: 0px 0.36rem;
		text-align: center;
		font-size: 0.36rem;
		color: #4d4d4d;
		width: 35%;
		height: 100%;
		border: 0px;
		background-color: rgba(255,255,255,0);
		background-image: url('../../static/images/ico_07.png');
		background-position: center right;
		background-repeat: no-repeat;
		background-size: 0.22rem 0.11rem;
	}
	.s_bg option{
		color: #4d4d4d;
		text-align: center;
	}
	.goback{
		background-image: url('../../static/images/pic_fh01.png');
	}
	.select{
		width: 100%;
		height: 1rem;
		background-color: #fff;
		background-image: url('../../static/images/ico_17.png');
		background-size: 0.35rem 0.1rem;
		background-repeat: no-repeat;
		background-position: 4.3rem center;
		border-top: 0.01rem solid #e7e7e7;
		border-bottom: 0.01rem solid #e7e7e7;
		margin-bottom: 0.2rem;
	}
	.select p{
		width: 100%;
		height: 100%;
		line-height: 1rem;
		font-size: 0.35rem;
		color: #222222;
		text-align: center;
		padding-right: 0.5rem;
	}
	.options{
		width: 100%;
		background-color: #fff;
		position: absolute;
		top: 3rem;
		left: 0px;
		z-index: 99999;
		padding: 0px 0.2rem;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.options p{
		width: 100%;
		height: 0.8rem;
		border-bottom: 0.01rem solid #e7e7e7;
		text-align: center;
		line-height: 0.8rem;
		font-size: 0.33rem;
	}
	.options p:last-child{
		border: 0px;
	}
	.i{
		margin-bottom: 0.2rem;
		background-color: #fff;
		overflow: hidden;
		padding: 0.3rem 0.3rem;
		border-bottom: 0.01rem solid #ebebeb;
	}
	.icon{
		float: left;
		display: inline-block;
		width: 0.1rem;
		height: 0.47rem;
		background-color: #40cc8b;

	}
	p{
		height: 0.47rem;
		line-height: 0.47rem;
	}
	.title{
		margin-left: 0.35rem;
		float: left;
		font-size: 0.33rem;
		color: #333333;
	}
	.data{
		font-size: 0.3rem;
		float: right;
		color: #808080;
	}
	.data span{
		color: red;
	}
	.capacity{
		background-color: #fff;
		margin-top: 0.18rem;
		border-top: 0.01rem solid #ebebea;
		border-bottom: 0.01rem solid #ebebea;
	}
	.c_top{
		padding: 0.27rem 0.33rem;
		border-bottom: 0.01rem solid #ebebeb;
	}
	.i_bs{
		clear: both;
		width: 100%;
		padding: 0.2rem 0.2rem;
		border-top: 0.01rem solid #e7e7e7;
		margin-top: 0.6rem;
	}
	.i_b{
		display: flex;
		flex-flow: row;
		font-size: 0.30rem;
		color: #777777;
	}
	.i_b span{
		display: block;
		width: 4rem;
	          overflow: hidden;
	          white-space: nowrap;
	          text-overflow: ellipsis;
	}
	.c_top span{
		display: inline-block;
		width: 0.1rem;
		height: 0.48rem;
		background-color: #40cc8b;
	}
	.c_top p{
		display: inline-block;
		font-size: 0.35rem;
		color: #333333;
		margin-left: 0.2rem;
		vertical-align: top;
		height: 0.48rem;
		line-height: 0.48rem;
	}
	.c_exp{
		width: 100%;
		padding: 0.87rem;
		overflow: hidden;
	}
	.c_c{
		float: left;
		width: 2.32rem;
	}
	.c_c:nth-child(2n){
		margin-left: 1.11rem;
	}
	.c_c:nth-child(n+3){
		margin-top: 0.83rem;
	}
	.c_c p{
		margin-top: 0.25rem;
		color: #555555;
		font-size: 0.35rem;
		text-align: center;
		width: 100%;
	}
	.c_c p.c_c_t{
		margin-top: 0.05rem;
		color: #777777;
		font-size: 0.28rem;
		height: 0.3rem;
		line-height: 0.3rem;
	}
/*	.footer{
		background-color: #fff;
		margin-top: 0.88rem;
		display: flex;
		flex-flow: row;
		width: 100%;
		height: 1.11rem;
		border-top: 0.01rem solid #e7e7e7;
	}
	.gk{
		flex: 1;
		padding: 0.33rem 0px;
	}
	.gk p{
		color: #40cc8b;
		border-right: 0.01rem solid #e6e6e6;
		padding-left: 0.7rem;
		font-size: 0.4rem;
		width: 100%;
		text-align: center;
		background-image: url('../../static/images/ico_15.png');
		background-repeat: no-repeat;
		background-size: 0.41rem 0.41rem;
		background-position: 1rem center;
	}
	.xq{
		flex: 1;
		color: #808080;
		font-size: 0.4rem;
		text-align: center;
		background-image: url('../../static/images/ico_16No.png');
		background-repeat: no-repeat;
		background-size: 0.41rem 0.41rem;
		background-position: 1rem center;
	}
	.xq p{
		height: 1.1rem;
		line-height: 1.1rem;
		padding-left: 0.7rem;
	}*/
	.footer{
		background-color: #fff;
		margin-top: 0.88rem;
		display: flex;
		flex-flow: row;
		width: 100%;
		height: 1.11rem;
		border-top: 0.01rem solid #e7e7e7;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.gk{
		position: relative;
		flex: 1;
		padding: 0.33rem 0px;
	}
	.gk:after{
		content: '';
		position: absolute;
		left: 0px;
		bottom: 0px;
		width: 100%;
		height: 0.1rem;
		border-bottom: 0.1rem solid #40cc8b;
	}
	.gk p{
		color: #40cc8b;
		border-right: 0.01rem solid #e6e6e6;
		font-size: 0.4rem;
		width: 100%;
		text-align: center;
	}
	.xq{
		flex: 1;
		color: #808080;
		font-size: 0.4rem;
		text-align: center;
	}
	.xq p{
		height: 1.1rem;
		line-height: 1.1rem;
	}
</style>


