<template>
	<div>
		<div class="tips">
			<p>{{msg}}</p>
			<div class="btn">
				<button @click="_del">删除</button>
				<button @click="_no">取消</button>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		props: ['msg'],
		methods: {
			// 确定删除
			_del: function(){
				this.$emit('del');
			},
			// 不删除
			_no: function(){
				this.$emit('no');
			}
		}
	}
</script>

<style scoped>
	.tips{
		z-index: 9;
		display: block;
		position: absolute;
		top: 50%;
		left: 50%;
		width: 5.4rem;
		height: 3rem;
		margin-top: -1.5rem;
		margin-left: -2.7rem;
		border-radius: 0.1rem;
		background-color: rgba(255,255,255,0.9);
		border: 0.02rem solid #e7e7e7;
	}
	.tips p{
		width: 100%;
		height: 2.12rem;
		padding: 0.5rem;
		font-size: 0.35rem;
		line-height: 1.12rem;
		text-align: center;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.btn{
		display: flex;
		width: 100%;
		height: 0.88rem;
		border-bottom: 0.02rem solid #e7e7e7
	}
	.btn button{
		flex: 1;
		border: 0px;
		border-right: 0.01rem solid #e7e7e7;
		text-align: center;
		line-height: 0.88rem;
		color: red;
		font-size: 0.3rem;
		background-color: #fff;
	}
	.btn button:nth-child(2){
		color: rgb(120,120,120);
		border: 0px;
	}
</style>


