<template>
	<div>
		<header>
			<a href="javascript:void(0)"  class="goback" @click="_goto" v-if='!t_app'></a>
			<router-link to='/fbrw' class='goback' v-if='t_app'></router-link>
			<p>发布任务</p>
		</header>
		<div class="content">
			<div class="c_m">
				<div class="c_b">
					<p class="c_ti">选择考核班级</p>
					<div class="c_b_l">
						<div v-for='(c, i) in c_list' :key='i' :class='c.id | dSye(cd_list)'>
							<input :value="c.id" type="checkbox" name="" :id='"bj"+i' v-model='cd_list'>
							<label :for='"bj"+i'><p>{{c.name}}</p></label>
						</div>
					</div>
				</div>
				<div class="c_k">
					<p class="c_ti">选择考核方式</p>
					<div class="a_mth">
						<div v-for='(a, i) in a_list' :key='a.name'>
							<input :id='"a"+i' class="a_c" type="checkbox" name="a_mth" :value='a.val' v-model='khfs' :style='a.val | inSye(khfs)'>
							<label :for='"a"+i'>
								<p class="a_name" :style='a.val | pSye(khfs)'>{{a.name}}</p>
							</label>
						</div>
					</div>
				</div>
				<div class="c_py">
					<p class="c_ti">阅读要求(选填)</p>
					<textarea placeholder="您的推荐能帮助学生更好地理解本次任务..." v-model='ydyq'></textarea>
					<!-- <button @click='_sY'></button> -->
				</div>
				<div class="yy" v-show='y_flag'>
					<button v-ly></button>
					<p>按住说话</p>
				</div>
				<div class="cc_yy" v-show='yy_flag' @click='_play'>{{y_time}}s</div>
				<router-link to='/fbrw/ts' class='c_nt'>下一步</router-link>
				<p class="c_t">共3步</p>
				<div class="time" v-show='y_tflag'>
					<p>{{y_time}}<span>s</span></p>
				</div>
			</div>
		</div>
		<tips :msg='message' v-show='t_flag'></tips>
	</div>
</template>

<script>
	// 用于给自定义事件提供Vue实例
	var that=null;
	var timer=null;
	import tips from './msgtips.vue'
	export default {
		data(){
			return {
				// 班级列表
				c_list: [],
				// 选中班级
				cd_list: [],
				// 班级
				cl_flag: -1,
				// 考核方式
				khfs: [],
				a_list: [
					{val: 1, name: '评测'},
					{val: 2, name: '写读后感'}
				],
				// 阅读要求
				ydyq: '',
				// 提示框
				message: '',
				t_flag: false,
				// 语音
				y_flag: false,
				localId: '',
				y_time: 0,
				// 时间记录
				y_tflag: false,
				// 语音条
				yy_flag: false,
				// 语音播放状态
				// false为停止状态 true为播放状态
				y_state: false,
				t_app: false
			}
		},
		created: function(){
			var type = this.util.getAppScene();
			if(type == 'app'){//app应用端调用
				this.t_app=true;
			}else{
				this.t_app=false;
			}
			that=this;
			var self=this;

			// --------------请求班级列表begin--------------
			this.c_list=this.util.getSessionData('clist');
			this.cd_list.push(this.c_list[0].id);
			this.khfs.push(this.a_list[0].val);
			// --------------请求班级列表end--------------

			// 微信SDK
			var ret=this.util.getSessionData('rets');
			wx.config({
				debug: false,
				appId: ret.data.appID,
				timestamp: ret.data.timestamp,
				nonceStr: ret.data.nonceStr,
				signature: ret.data.signature,
				jsApiList: [
					'startRecord',
					'stopRecord',
					'playVoice',
					'pauseVoice',
					'stopVoice',
					'onVoicePlayEnd',
					'uploadVoice'
				]
			});
		},
		methods: {
			_sY: function(){
				if(this.y_flag){
					this.y_flag=false;
				}else{
					this.y_flag=true;
				}
			},
			_play: function(event){
				var self=this;
				if(this.y_state){
					wx.stopVoice({
					    localId: self.localId // 需要停止的音频的本地ID，由stopRecord接口获得
					});
					this.y_state=false;
					event.target.style.backgroundImage='url("../../static/images/xs_pic_yy.png")';
				}else{
					wx.playVoice({
						localId: self.localId // 需要播放的音频的本地ID，由stopRecord接口获得
					});
					this.y_state=true;
					event.target.style.backgroundImage='url("../../static/images/xs_pic_yy.gif")';
					wx.onVoicePlayEnd({
						success: function (res) {
							var localId = res.localId; // 返回音频的本地ID
							this.y_state=true;
							event.target.style.backgroundImage='url("../../static/images/xs_pic_yy.png")';
						}
					});
				}
			},
			_goto: function(){
				window.history.back();
			}
		},
		// 导航加载钩子
		beforeRouteEnter(to, from, next){
			if(from.path=='/fbrw/ts'||from.path=='/fbrw'){
				// 当从下一步返回到该页面时，恢复原有信息
				next(function(vm){
					var rwInfo=vm.util.getSessionData("rwinfo");
					if(rwInfo.bj&&rwInfo.khfs){
						vm.cd_list=rwInfo.bj.split(',');
						vm.khfs=rwInfo.khfs.split(',');
						vm.ydyq=rwInfo.ydyq;
					}
				})
			}else{
				next();
			}
		},
		// 导航离开钩子
		beforeRouteLeave(to, from, next){
			if(to.path=='/fbrw/ts'){
				if(this.cd_list.length==0||this.khfs.length==0){
					var self=this;
					clearTimeout(timer);
					timer=null;
					this.message='请填写重要信息';
					this.t_flag=true;
					timer=setTimeout(function(){
						self.t_flag=false;
					}, 2000);
					next(false);
				}else{
					var rwInfo=this.util.getSessionData("rwinfo");
					rwInfo.bj=this.cd_list.join();
					rwInfo.khfs=this.khfs.join();
					rwInfo.ydyq=this.ydyq;
					this.util.setSessionData("rwinfo", rwInfo);
					next();
				}
			}else{
				next();
			}
		},
		components: {
			"tips": tips
		},
		directives: {
			binit: {
				inserted: function(el){
					var calendar = new lCalendar();
					calendar.init({
					    'trigger': '#t_begin',
					    'type': 'datetime'
					});
				}
			},
			einit: {
				inserted: function(el){
					var calendar = new lCalendar();
					calendar.init({
					    'trigger': '#t_end',
					    'type': 'datetime'
					});
				}
			},
			ly: {
				inserted: function(el){
					el.addEventListener('touchstart', function(){
						wx.startRecord();
						// 显示计时器
						that.y_tflag=true;
						// 时间清零
						that.y_time=0;
						// 隐藏语音条
						that.yy_flag=false;
						timer=setInterval(function(){
							that.y_time+=1;
						}, 1000);
					});
					el.addEventListener('touchend', function(){
						wx.stopRecord({
							success: function(res){
								that.localId=res.localId;
								// 关闭计时器
								that.y_tflag=false;
								// 显示语音条
								that.yy_flag=true;
								// 关闭语音按钮
								that.y_flag=false;
								clearInterval(timer);
								timer=null;
								// 语音上传到微信服务器
								wx.uploadVoice({
									localId: res.localId, // 需要上传的音频的本地ID，由stopRecord接口获得
									isShowProgressTips: 0, // 默认为1，显示进度提示
									success: function (res) {
										var serverId = res.serverId; // 返回音频的服务器端ID
									}
								});
							}
						});
					});
				}
			}
		},
		filters: {
			inSye: function(val){
				var flag=false;
				for(let i=0;i<arguments[1].length;i++){
					if(arguments[1][i]==val){
						flag=true;
						break;
					}
				}
				if(flag){
					return {backgroundImage: "url('../../static/images/radio_0ff.png')"}
				}else{
					return {backgroundImage: "url('../../static/images/Radio_no.png')"}
				}
			},
			pSye: function(val){
				var flag=false;
				for(let i=0;i<arguments[1].length;i++){
					if(arguments[1][i]==val){
						flag=true;
						break;
					}
				}
				if(flag){
					return {color: "#777777"}
				}else{
					return {color: "#b7b7b7"}
				}
			},
			dSye: function(val){
				var flag=false;
				for(let i=0;i<arguments[1].length;i++){
					if(arguments[1][i]==val){
						flag=true;
						break;
					}
				}
				if(flag){
					return "bg";
				}else{
					return "";
				}
			}
		}
	}
</script>

<style scoped>
	.content{
		position: absolute;
		top: 0.88rem;
		bottom: 0px;
		width: 100%;
		background-color: #fff;
		padding: 0.3rem;
	}
	.c_m{
		width: 100%;
		height: 100%;
		overflow: auto;
	}
	.c_b,.c_k{
		width: 100%;
		margin-bottom: 0.8rem;
	}
	.c_k{
		margin-top: 0.3rem;
	}
	.c_b_l{
		width: 100%;
		display: flex;
		flex-wrap: wrap;
	}
	.c_b_l div{
		margin-top: 0.2rem;
		margin-left: 0.25rem;
		border: 0px;
		background-color: #fff;
		border: 0.01rem solid #777777;
		border-radius: 0.1rem;
	}
	.c_b_l div.bg{
		background-color: #ddfdeb;
		background-image: url('../../static/images/xg.png');
		background-size: 0.35rem 0.35rem;
		background-repeat: no-repeat;
		background-position: right top;
	}
	.c_b_l div p{
		padding: 0.2rem 0.3rem;
		text-align: center;
		font-size: 0.35rem;
	}
	.c_b_l div input{
		display: none;
	}
	.c_py{
		position: relative;
		margin-top: 0.8rem;
		width: 100%;
	}
	.c_ti{
		border-left: 0.1rem solid #40cc8b;
		font-size: 0.35rem;
		padding-left: 0.1rem;
	}
	.c_py textarea{
		margin-top: 0.3rem;
		width: 100%;
		resize: none;
		border-radius: 0.1rem;
		height: 3rem;
		border: 0.01rem solid #e7e7e7;
		padding: 0.2rem;
		font-size: 0.33rem;
	}
	.c_py button{
		border: 0px;
		background-color: rgba(0,0,0,0);
		position: absolute;
		bottom: 0.2rem;
		right: 0.15rem;
		width: 0.45rem;
		height: 0.65rem;
		background-image: url('../../static/images/ico_02.png');
		background-size: 100% 100%;
	}
	.c_nt{
		display: block;
		width: 3rem;
		height: 1rem;
		background-color: #40cc8b;
		color: #fff;
		font-size: 0.35rem;
		text-align: center;
		line-height: 1rem;
		border-radius: 3rem;
		margin: 0 auto;
		margin-top: 0.3rem;
	}
	.c_t{
		margin-top: 0.2rem;
		text-align: center;
		font-size: 0.3rem;
		color: #777777;
	}
	.a_mth{
		overflow: auto;
		overflow-y: hidden;
		display: box;
		display: -webkit-box;
		width: 100%;
		height: 0.54rem;
		margin-top: 0.33rem;
	}
	.a_c{
		-webkit-appearance: none;
		background-image: url('../../static/images/Radio_no.png');
		background-size: 100% 100%;
		width: 0.54rem;
		height: 0.54rem;
		border: 0px;
	}
	.a_c:checked{
		background-color: #fff;
	}
	.a_name{
		display: inline-block;
		height: 0.54rem;
		line-height: 0.54rem;
		font-size: 0.42rem !important;
		vertical-align: top;
		color: #b7b7b7;
		margin-right: 0.8rem;
	}
	.a_add,.a_n_list{
		position: relative;
		width: 100%;
	}
	.a_add input{
		width: 100%;
		height: 0.9rem;
		border: 0px;
		border-bottom: 0.01rem solid #e7e7e7;
		padding-left: 0.2rem;
	}
	.a_add a{
		display: block;
		width: 0.5rem;
		height: 0.5rem;
		border: 0px;
		background-color: #fff;
		background-image: url('../../static/images/ico_24.png');
		background-size: 100% 100%;
		position: absolute;
		top: 50%;
		right: 0.2rem;
		margin-top: -0.25rem;
	}
	.a_checed{
		color: #111111;
	}
	.yy{
		margin-top: 0.2rem;
		width: 100%;
	}
	.yy button{
		display: block;
		width: 1.8rem;
		height: 1.8rem;
		margin: 0 auto;
		background-image: url('../../static/images/yy.png');
		background-color: rgba(0,0,0,0);
		background-size: 100% 100%;
		border: 0px;
	}
	.yy p{
		font-size: 0.33rem;
		color: #555555;
		text-align: center;
		margin-top: 0.2rem;
	}
	.cc_yy{
		/*float: left;*/
		margin-top: 0.3rem;
		width: 3rem;
		height: 0.8rem;
		border: 0.01rem solid #e8e8e8;
		background-color: #f2f2f2;
		border-radius: 0.1rem;
		font-size: 0.35rem;
		color: #40cc8b;
		line-height: 0.8rem;
		padding-left: 0.2rem;
		background-image: url('../../static/images/xs_pic_yy.png');
		background-size: 0.2rem 0.35rem;
		background-position: 2.5rem center;
		background-repeat: no-repeat;
	}
	.time{
		position: fixed;
		width: 3rem;
		height: 3rem;
		background-color: rgba(0,0,0,0.8);
		top: 50%;
		left: 50%;
		margin-top: -1.5rem;
		margin-left: -1.5rem;
		border-radius: 0.1rem;
	}
	.time p{
		width: 100%;
		text-align: center;
		line-height: 3rem;
		color: #fff;
		font-size: 1.4rem;
		font-weight: 900;
	}
</style>


