<template>
	<div>
		<!-- 最底层 -->
		<div class="b_tier" :style="{ backgroundColor: clr}">
			<!-- 最上层 -->
			<div class="t_tier">
				<p :style="{color : clr}">{{data}}<span>%</span></p>
			</div>
			<!-- 左底层 -->
			<div class="r_b_tier" :style="[{backgroundColor: clr}, show]"></div>
			<!-- 旋转层 -->
			<div class="r_tier s_tier" :style="rotate"></div>
			<!-- 右遮蔽层 -->
			<div class="l_tier s_tier" :style='hidden'></div>
		</div>
	</div>
</template>

<script>
	export default {
		// 进度数值
		props: ['data','color'],
		data(){
			return {
				d: 0,
				clr: this.color,
				flag: false
			}
		},
		watch: {
			data: function(){
				var self=this;
				if(this.data<=50){
					this.d=this.data;
				}
				// 当数值大于50%时
				if(this.data>50){
					// 先让进度条读取50%
					this.d=50;
					// 进度条读取到50%后 读取剩下的进度
					// 左底层显示
					// 右遮蔽层隐藏
					setTimeout(function(){
						self.d=self.data;
						self.flag=true;
					}, 1050);
				}
			}
		},
		computed: {
			rotate: function(){
				var data=this.d*3.6;
				return {
					transform: 'rotate('+data+'deg)'
				}
			},
			show: function(){
				if(this.flag){
					return {
						zIndex: '6'
					}
				}else{
					return {
						zIndex: '4'
					}
				}
			},
			hidden: function(){
				if(this.flag){
					return {
						display: 'none'
					}
				}else{
					return {
						display: 'block'
					}
				}
			}
		}
	}
</script>

<style scoped>
	.b_tier{
		position: relative;
		z-index: 7;
		width: 2.32rem;
		height: 2.32rem;
		background-color: #f66c6c;
		border-radius: 100%;
		overflow: hidden;
	}
	.t_tier{
		position: absolute;
		top: 50%;
		left: 50%;
		margin-top: -0.975rem;
		margin-left: -0.975rem;
		z-index: 8;
		width: 1.95rem;
		height: 1.95rem;
		border-radius: 100%;
		background-color: #fff;
	}
	.t_tier p{
		width: 100%;
		height: 100%;
		text-align: center;
		line-height: 1.95rem;
		color: #f66c6c;
		font-size: 0.6rem;
		font-weight: 900;
	}
	.t_tier p span{
		font-size: 0.35rem;
		font-weight: 400;
	}
	.s_tier{
		position: absolute;
		top: 0px;
		width: 1.16rem;
		height: 2.32rem;
		background-color: #f3f3f3;
		transition: transform 1s cubic-bezier(0.5,0.5,0.5,0.5);
	}
	.r_tier{
		right: 0px;
		z-index: 5;
		-webkit-transform: rotate(0deg) ;
		-ms-transform: rotate(0deg) ;
		-o-transform: rotate(0deg) ;
		transform: rotate(0deg) ;
		transform-origin: left center;
	}
	.l_tier{
		left: 0px;
		z-index: 3;
	}
	.r_b_tier{
		position: absolute;
		z-index: 4;
		right: 0px;
		top: 0px;
		width: 1.16rem;
		height: 2.32rem;
		/*border-radius: */
		background-color: #f66c6c;
	}
</style>


