<template>
	<div style="background-color: #fff;">
		<header>
			<a href="javascript:void(0)"  class="goback" @click="_goto" v-if='!t_app'></a>
			<router-link to='/main' class='goback' v-if='t_app'></router-link>
			<p class="h_tit">已批阅读后感</p>
		</header>
		<div class="type">
			<div class="type_li" @click="_fn()" style=" border-right:#e6e6e6 0.01rem solid; ">
				<span>班级：{{c_value}}</span>
				<img src="../../static/images/ico_17.png" class="type_img" />
			</div>
		</div>
		<div class="content">
			<div class="type_foot"></div>

			<div class="jrrw">
				<!-- 任务列表 -->
				<div class="jrrw_list"  v-bind:class="'bj'+rw.state" v-for="rw in rw_list">
					<router-link :to="rw.state | href(rw.id)" >
					<div class="jrrw_info">
						<p class="rw_title"><span class="title_img">&nbsp;</span>{{rw.stuName}}&nbsp;&nbsp;同学</p>
						<div class="rw_time"><div>提交日期：{{rw.createDate}}</div> </div>
					</div>
					<p class="c_bg">{{rw.state | isState}}</p>
					<span v-show="_isNew(rw.createDate)"><img  src="../../static/images/xs_pic_newR.png"></span>
					</router-link>
				</div>
			</div>
		</div>
		<div class="type_li1 type_li2" v-if="willShow">
			<div class="type_ling" :class='{type_yes: flag==-1}' @click='_checked(-1, "", "全部")'>全部</div>
			<div class="type_ling" :class='{type_yes: flag==i}' @click='_checked(i, c.id, c.name)' v-for='(c, i) in c_list' :key='c.name'>{{c.name}}</div>
		</div>
		<div class="dc"></div>
		<tips :msg='message' v-show='t_flag'></tips>
	</div>
</template>

<script>
	var timer=null;
	import tips from './msgtips.vue'
	export default {
		data(){
			return {
				// 年级
				flag: -1,
				// 状态
				zt_flag: "",
				zt_value: "全部",
				willShow:false,
				willShow2:false,
				rw_list: [],
				// 班级列表
				c_list: [],
				// 选中班级的id  等接口完成后要改掉！！！
				c_clid: '',
				c_value: '全部',
				// 提示框
				message: '',
				t_flag: false,
				// 页码
				page: 1,
				// 上拉加载
				sl_flag: false,
				nextpage: true,
				t_app: false
			}
		},
		methods: {
			_fn:function(){
			          if(this.willShow==true){
			            this.willShow=false;
			          }else{
			            this.willShow=true;
			          }
	       	 	},
	        		_show:function(){
			          if(this.willShow2==true){
			            	this.willShow2=false;
			          }else{
			            	this.willShow2=true;
			          }
		        	},
		        	_checked: function(i){
		        		var self=this;
		        		this.flag=i;
		        		this.willShow=false;
		        		// 选择班级默认状态为全部
		        		this.zt_flag='';
		        		this.zt_value='全部';
		        		// 选中班级的id
		        		this.c_clid=arguments[1];
		        		this.c_value=arguments[2];
		        		this.page=1;
				// 加载数据
				this._load(this.page);
		        	},
			_isNew: function(value){
				var today=this.util.getDate();
				if(value==today){
					return true;
				}else{
					return false;
				}
			},
			// 加载数据
			_load: function(page){
				var self=this;
				// 上拉加载
				if(!this.sl_flag){
					// 清空列表
					this.rw_list=[];
				}
				// 提示框
				this.message='加载中';
				this.t_flag=true;
				// --------------请求报告列表begin--------------
				this.util.request(
				{
					url: self.globalurl.host+self.globalurl.url.pydhg,
					data: {"loginAccount": self.util.getSessionData("userName"),"classId": self.c_clid,"page": page, "pageSize": 10,"state": 1},
					dataType: 'json',
					type: 'POST'
				}, function(data){
					if(data.status==200&&data.result.length!=0){
						for(let i=0;i<data.result.length;i++){
							self.rw_list.push(data.result[i]);
						}
						self.nextpage=data.nextPage;
						self.t_flag=false;
						self.sl_flag=false;
					}
					if(data.status=404){
						clearTimeout(timer);
						timer=null;
						self.message='暂无数据';
						timer=setTimeout(function(){
							self.t_flag=false;
						}, 2000);
						self.sl_flag=false;
					}
					if(data.status==500){
						clearTimeout(timer);
						timer=null;
						self.message='服务器开小差啦，请稍后再试';
						self.t_flag=true;
						timer=setTimeout(function(){
							self.t_flag=false;
						}, 2000);
						self.sl_flag=false;
					}
				}, self)
				// --------------请求报告列表end--------------
			},
			// 状态
			_isCheck: function(flag){
				var self=this;
				this.zt_flag=flag;
				if(flag===0){
					this.zt_value='未批阅';
				}else if(flag===''){
					this.zt_value='全部';
				}else if(flag==1){
					this.zt_value='已批阅';
				}
				this.willShow2=false;
				this.page=1;
				// 加载数据
				this._load(this.page);
			},
			_goto: function(){
				window.history.back();
			}
		},
		created: function(){
			var type = this.util.getAppScene();
			if(type == 'app'){//app应用端调用
				this.t_app=true;
			}else{
				this.t_app=false;
			}
			var self=this;
			// --------------请求班级列表begin--------------
			this.c_list=this.util.getSessionData('clist');
			// --------------请求班级列表end--------------

			// 加载数据
			this._load(this.page);

			// 上拉加载
			window.addEventListener('scroll', function(){
				if(document.body.scrollTop+document.documentElement.clientHeight==document.body.scrollHeight){
					if(self.$route.path=='/pydhg'){
						if(self.nextpage){
							self.page+=1;
							self.sl_flag=true;
							self._load(self.page);
						}else{
							clearTimeout(timer);
							timer=null;
							self.t_flag=true;
							self.message='数据已到底';
							timer=setTimeout(function(){
								self.t_flag=false;
							}, 2000);
						}
					}
				}
			})
		},
		filters: {
			state: function(value){
				if(value==1){
					return '已查阅';
				}else{
					return '未查阅';
				}
			},
			href: function(value){
				if(value==1){
					return '/pydhg/yxq?id='+arguments[1];
				}else{
					return '/pydhg/xq?id='+arguments[1];
				}
			},
			isState: function(value){
				if(value==1){
					return '已批阅';
				}else{
					return '未批阅';
				}
			}
		},
		components: {
			"tips": tips
		}
	}
</script>

<style scoped>
	/*底层颜色*/
	.dc{
		position: absolute;
		top: 0px;
		bottom: 0px;
		width: 100%;
		background-color: #fff;
		z-index: 1;
	}
	.content{
		position: relative;
		top: 0.88rem;
		bottom: 1.11rem;
		width: 100%;
		background-color: #fff;
		padding: 0.3rem 0;
		padding-top: 0;
		overflow: auto;
		z-index: 3;
	}
	.type{
		position: fixed;
		top: 0.88rem;
		z-index: 99999999;
		height: 0.95rem; 
		width: 100%; 
		font-size: 0.32rem;
		color: #666; 
		display: flex; 
		background-color: #fff;
		/*border-bottom: 0.1rem solid rgba(0,0,0,0);*/
		align-items: center; 
		justify-content: center; 
		background-image: url('../../static/images/bj_line.png'); 
		background-position: top -2px;
		/*background-size: 100% 0.1rem;*/
		background-repeat: repeat-x;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.type_li{
		position: relative; 
		width: 100%; 
		padding: 0 0.4rem; 
		display: flex; 
		margin-top: 0.08rem;
		align-items: center; 
		justify-content: center; 
	}
	.type_img{
		margin-left: 0.8rem;
		width: 0.35rem; 
		height: 0.18rem;
	}
	.type_li1{
		position: fixed;
		top: 1.88rem;
		right: 0;
		z-index: 9999999999; 
		background-color: #fff;  
		width: 100%;
		font-size: 0.3rem;
		line-height: 0.70rem;
		flex-direction: column; 
		align-items: center; 
		justify-content: center; 
		text-indent: 0.3rem; 
		border:#f1f1f1 0.01rem solid; 
		color: #666; 
	}
	.type_li2{
		left: 0;
	}
	.type_ling {
		text-align: center;
		height: 0.70rem;
		line-height: 0.70rem;
		border-bottom:#f1f1f1 0.01rem solid; 
	}
	.type_yes{ 
		background-color: #40cc8b;
		color: #fff;	
	 }
	.type_foot{
		width: 7.5rem; 
		height: 0.1rem; 
		background-image: url('../../static/images/bj_line.png');
		background-position: top -0.1rem; 
		background-repeat: repeat-x; 
		margin-bottom: 0.2rem; 	
	}
	.tips{
		display: flex;
		align-items: center;
		justify-content: left;
		padding: 0.25rem 0rem; 
		margin:0 0.2rem;
		border-top: 0.01rem solid #f1f1f1;
		font-size: 0.3rem;
		color: #79a8ec;
	}
	.tips p{ margin-right: 0.4rem; }
	.tips p:last-child{
		color: #808080;
	}
	

	.jrrw{
		margin-top: 1rem;
		width: 100%;
		height: 100%;
		background-color: #fff;
	}
	.title_img{
		background-color: #40cc8b; 
		margin-right: 0.1rem;
	}
	.jrrw_list{
		overflow: hidden;
		position: relative;
		z-index: 2;
		width: 95%; margin: 0 auto;
		background-color:#f1f6fd;
		border:0.01rem solid #40cc8b;
		border-radius: 0.12rem;
		margin-bottom: 0.2rem;
	}
	.jrrw_list span img{
		position: absolute;
		top: -0.02rem;
		right: -0.02rem;
		width: 0.75rem; height:0.7rem;
	}
	.jrrw_info{
		/*float: left;*/
		width: 5.4rem;
		padding: 0.2rem 0.3rem ;
	}
	.rw_title{
		font-size: 0.34rem;
		line-height: 0.65rem;
		color: #40cc8b;
	}
	.rw_title1{
		font-size: 0.3rem;
		line-height: 0.45rem;
		color: #666;
	}
	.rw_time{ 
		display: flex;
		align-items: center; 
		justify-content: space-between;
		width: 100%; height: 0.4rem; margin-top: 0.05rem;
		font-size: 0.26rem;
		color: #808080;
	}
	.c_bg{
		position: absolute;
		top: 0.7rem;
		right: 0;
		text-align: center;
		width: 1.6rem;
		height: 0.45rem;
		line-height: 0.45rem;
		font-size: 0.35rem; 
		font-weight: bold;
		color: #f60;
		font-family: sans-serif;
	}
	.bj0{ background-color: #eafff5;  }
	
	.bj1{ 
		background-color: #fff; 
		border:0.01rem solid #e7e7e7 ; 
	}
	.bj2{ background-color: #fff;  }
	.bj2 .c_bg p{
		 background-color: #fff; 
	}

</style>


