<template>
	<div>
		<header>
			<router-link to='/main' class="goback"></router-link>
			<p>个人资料</p>
			<a href="javascript:void(0)" class='qd' @click='_send'>确定</a>
		</header>
		<div class="tx">
			<div v-if="t_app">
				<label @click='_choose' class="tx_l">
					<img :src="src" class="itx" id="asdsadad">
					<img src="../../static/images/wd_10.png" @click='_uploadImg' class="tj">
				</label>
			</div>
			<div v-if="!t_app">
				<label for="tx_gg" @click='_choose' class="tx_l">
					<img :src="src" class="itx">
					<img src="../../static/images/wd_10.png" class="tj">
				</label>
				<form id='tx_form'>
					<input type="file" name="file" style="display: none" id="tx_gg">
				</form>
			</div>
		</div>

		<div class="main">
			<div class="n_s">
				<div class="name">
					<p>姓名<span>*</span></p>
					<input type="text" name="" v-model='name'>
				</div>
				<div class="sex">
					<p>性别<span>*</span></p>
					<select v-model='sex'>
						<option value="男">男</option>
						<option value="女">女</option>
					</select>
				</div>
			</div>
			<p>电话<span>*</span></p>
			<input type="text" name="" v-model='phone' maxlength="11">
			<p>QQ<span>*</span></p>
			<input type="text" name="" v-model='qq' maxlength="11" >
			<p>邮箱<span>*</span></p>
			<input type="text" name="" v-model='email'>
			<p>学校<span>*</span></p>
			<input type="text" readonly="readonly" name="" v-model='sname'>
		</div>
		<tips :msg='message' v-show='t_flag'></tips>
	</div>
</template>

<script>
	var timer=null;
	import tips from './msgtips.vue'
	export default {
		data(){
			return {
				// 提示框
				message: '',
				t_flag: false,
				src: '',
				name: '',
				sname: '',
				qq: '',
				phone: '',
				sex: '',
				email: '',
				// 旧数据
				osrc: '',
				oname: '',
				osname: '',
				oqq: '',
				ophone: '',
				osex: '',
				oemail: '',
				t_app: false
			}
		},
		created: function(){
			var self=this;
			this.message='加载中';
			this.t_flag=true;
			this.util.request(
			{
				url: self.globalurl.host+self.globalurl.url.grxx,
				data: {
					"loginAccount": self.util.getSessionData("userName")
				},
				dataType: 'json',
				type: 'POST'
			}, function(data){
				if(data.status==200){
					self.name=data.result.name;
					self.sname=data.result.schoolName;
					self.qq=data.result.qq;
					self.phone=data.result.phone;
					self.sex=data.result.sex;
					self.src=data.result.avatar;
					self.email=data.result.email;
					self.oname=data.result.name;
					self.osname=data.result.schoolName;
					self.oqq=data.result.qq;
					self.ophone=data.result.phone;
					self.osex=data.result.sex;
					self.osrc=data.result.avatar;
					self.oemail=data.result.email;
					self.t_flag=false;
				}
				if(data.status==404){
					clearTimeout(timer);
					timer=null;
					self.message='暂无数据';
					timer=setTimeout(function(){
						self.t_flag=false;
					}, 2000);
				}
				if(data.status==500){
					clearTimeout(timer);
					timer=null;
					self.message='服务器开小差啦，请稍后再试';
					self.t_flag=true;
					timer=setTimeout(function(){
						self.t_flag=false;
					}, 2000);
				}
			}, self)

			window.changeUserImg = function(url){//Android调用修改用户头像
				if(url != ''){
					self.src = url;
				}
			}
			
			var type = this.util.getAppScene();
			if(type == 'app'){//app应用端调用
				this.t_app=true;
			}else{
				this.t_app=false;
			}
		},
		components: {
			'tips': tips
		},
		methods: {
			_uploadImg:function(){
				var self=this;
				var type = this.util.getAppScene();
				if(type == 'app'){//app应用端调用
					if(window.js && window.js.updateUserInfo){
						var token = self.util.getSessionData('token');
						window.js.updateUserInfo(token);//app调用头像上传功能
					}
				}
			},
			_choose: function(){
				var self=this;
				var el=event.target.parentNode.parentNode.getElementsByTagName("input")[0];
				el.addEventListener('change', function(){
					var img=el.files[0];
					console.log(img);
					if(img){
						// 图片格式判断
						if(img.type.indexOf('image')==0 && img.type && /\.(?:jpg|png|gif|JPG|PNG|GIF)$/.test(img.name)) {
							var form = document.getElementById('tx_form');
							var formData = new FormData(form);
							$.ajax({
								url: self.globalurl.host+self.globalurl.url.scwj+'?token='+self.util.getSessionData('token'),
								data: formData,
								dataType: 'json',
								type: 'POST',
							          processData: false,
							          contentType: false,
								success: function(data){
									if(data.status==200){
										self.src=data.result.url;
									}
								},
								error:function(e){
									clearTimeout(timer);
									timer=null;
									self.message='图片大小不能超过3MB';
									self.t_flag=true;
									timer=setTimeout(function(){
										self.t_flag=false;
									}, 2000);
								}
							})
						}else{
							clearTimeout(timer);
							timer=null;
							self.message='请选择图片';
							self.t_flag=true;
							timer=setTimeout(function(){
								self.t_flag=false;
							}, 2000);
							return false;
						}
					}
				});
			},
			_send: function(){
				var self=this;
				// 判断数据是否为空
				if(this.name!=''&&this.sex!=''&&this.phone!=''&&this.qq!=''&&this.email!=''&&this.src!=''){
					// 判断是否有更改数据
					if(this.name!=this.oname||this.sex!=this.osex||this.phone!=this.ophone||this.qq!=this.oqq||this.email!=this.oemail||this.src!=this.osrc){
						if(!(/^1[3|4|5|8][0-9]\d{4,8}$/.test(this.phone))){
							clearTimeout(timer);
							timer=null;
							this.message='请输入正确手机号码';
							this.t_flag=true;
							timer=setTimeout(function(){
								self.t_flag=false;
							}, 2000);
						}else if(!(/^([0-9A-Za-z\-_\.]+)@([0-9a-z]+\.[a-z]{2,3}(\.[a-z]{2})?)$/g.test(this.email))){
							clearTimeout(timer);
							timer=null;
							this.message='请输入正确邮箱';
							this.t_flag=true;
							timer=setTimeout(function(){
								self.t_flag=false;
							}, 2000);
						}else if(!(/^\d{5,10}$/.test(this.qq))){
							clearTimeout(timer);
							timer=null;
							this.message='请输入正确qq号';
							this.t_flag=true;
							timer=setTimeout(function(){
								self.t_flag=false;
							}, 2000);
						}else{
							this.util.request(
							{
								url: self.globalurl.host+self.globalurl.url.xgxx,
								data: {
									"loginAccount": self.util.getSessionData("userName"),
									"name": self.name,
									"sex": self.sex,
									"phone": self.phone,
									"qq": self.qq,
									"email": self.email,
									"avatar": self.src
								},
								dataType: 'json',
								type: 'POST'
							}, function(data){
								if(data.status==200){
									clearTimeout(timer);
									timer=null;
									self.message='保存成功';
									self.t_flag=true;
									timer=setTimeout(function(){
										self.t_flag=false;
									}, 2000);
								}
								if(data.status==500){
									clearTimeout(timer);
									timer=null;
									self.message='服务器开小差啦，请稍后再试';
									self.t_flag=true;
									timer=setTimeout(function(){
										self.t_flag=false;
									}, 2000);
								}
							}, self)
						}
					}else{
						clearTimeout(timer);
						timer=null;
						this.message='数据无更改，无需保存';
						this.t_flag=true;
						timer=setTimeout(function(){
							self.t_flag=false;
						}, 2000);
					}
				}else{
					clearTimeout(timer);
					timer=null;
					this.message='数据不能为空';
					this.t_flag=true;
					timer=setTimeout(function(){
						self.t_flag=false;
					}, 2000);
				}
			}
		}
	}
</script>

<style scoped>
	header{
		background-color: #fff;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.goback{
		background-image: url('../../static/images/pic_fh01.png');
	}
	header p{
		color: #333333;
		font-weight: 500;
	}
	.qd{
		position: absolute;
		top: 0.3rem;
		right: 0.3rem;
		font-size: 0.35rem;
		color: #40cc8b;
	}
	.tx{
		position: relative;
		margin-top: 0.88rem;
		width: 100%;
		padding: 0.52rem 0px;
		background-color: #fff;
	}
	.tx .itx{
		display: block;
		margin: 0 auto;
		width: 1.7rem;
		height: 1.7rem;
		border-radius: 100%;
	}
	.tx .tj{
		display: block;
		position: absolute;
		top: 1.25rem;
		right: 0.05rem;
		width: 0.5rem;
		height: 0.5rem;
	}
	.tx_l{
		position: relative;
		width: 1.7rem;
		margin: 0 auto;
		height: 1.7rem;
		display: block;
	}
	.main{
		padding: 0.3rem;
		background-color: #fff;
	}
	.main p{
		font-size: 0.26rem;
		color: #b7b7b7;
	}
	.main p span{
		color: #40cc8b;
	}
	.main input,.main select{
		height: 0.9rem;
		width: 100%;
		font-size: 0.35rem;
		color: #333333;
		border: 0.01rem solid #e7e7e7;
		padding-left: 0.3rem;
		margin-top: 0.15rem;
		margin-bottom: 0.47rem;
	}
	.n_s input{
		width: 3.3rem;
	}
	.n_s{
		width: 100%;
		display: flex;
		flex-flow: row;
	}
	.name{
		flex: 1;
	}
	.sex{
		flex: 1;
	}
	.sex select{
		background-color: #fff;
	}
	.main .pas{
		background-image: url('../../static/images/wd_11.png');
		background-size: 0.4rem 0.17rem;
		background-repeat: no-repeat;
		background-position: 6rem center;
	}
</style>


