<template>
	<div id="ydzq" style=" background-color: #fff; font-family: 黑体; ">
		<header>
			<a href="javascript:void(0)"  class="goback" @click="_goto" v-if='!t_app'></a>
			<router-link to='/' class='goback' v-if='t_app'></router-link>
			<router-link to='/ydjy/ss' class="search">搜索资源</router-link>
		</header>
		<div class="nav">
			<ul  class="nav_li" >
				<li v-for='(item,index) in data'  @click='_load(item.id)' :key='item.id'><span v-bind:class="{'navYes':flag==item.id}">{{item.name}}</span></li>
			</ul>
		</div>
		<div class="ydjy_list" v-for="(ydzq,i) in ydzq_list" :key='ydzq.id'>
			<div class="ydjy_l"><img :src="ydzq.type | src"></div>
			<div class="ydjy_r">
				<div class="hdzq_titil" >{{ydzq.name}}</div>
				<dl>
					<dt style=" float: left; ">
						<img :src="ydzq.avatar" class="headImg"></img>
					</dt>
					<p class="hdzq_time">{{ydzq.author}}</p>
					<p class="hdxq_school">{{ydzq.schoolName}}</p>
				</dl>
				<ul class="record">
					<li><img src="../../static/images/ico_heart.png" class="pic_praise"></img >&nbsp;{{ydzq.love}}</li>
					<li>
						<form action='/v1/app/res/download' method="GET" v-if='!t_app'>
							<input type="hidden" name="resourceId" :value='ydzq.id'>
							<input type="hidden" name="loginAccount" :value='loginAccount'>
							<input type="hidden" name="token" :value='token'>
							<input type="submit" name="" :id="'sub'+i" style='display: none;'>
							<label :for="'sub'+i" style='color: #999;'>
								<img src="../../static/images/ico-down.png" class="pic_download"></img>&nbsp;{{ydzq.download}}
							</label>
						</form>
						<div v-if='t_app' @click='_download(ydzq.id, ydzq.name, loginAccount, token, ydzq.type)'>
							<img src="../../static/images/ico-down.png" class="pic_download"></img>&nbsp;{{ydzq.download}}
						</div>
					</li>
				</ul>
			</div>
		</div>
		<tips :msg='message' v-show='show'></tips>
	</div>
</template>
<script>
	var timer=null;
	import tips from './msgtips.vue'
	export default {
		data(){
			return {
				// 提示框消息
				message: '暂无数据',
				show: false,
				// 阅读教研 列表
				ydzq_list: [],
				data:[],
				flag:0,
				// 页数
				page: 1,
				// 上拉加载
				sl_flag: false,
				// 账号
				loginAccount: '',
				token: '',
				nextpage: true,
				t_app: false
			}
		},
		name: 'ydzq',
		methods: {
			_load:function(index){
				var self=this;
	           		this.flag=index;
	           		if(!this.sl_flag){
	           			this.ydzq_list=[];
	           			this.page=1;
				}
				this.message='加载中';
				this.show=true;
				// --------------请求资源列表begin--------------
				this.util.request(
				{
					url: self.globalurl.host+self.globalurl.url.zylb,
					data: {"moduleId":self.flag,"page":self.page,"pageSize": "10"},
					dataType: 'json',
					type: 'POST'
				}, function(data){
					if(data.statusCode==200&&data.resources.length!=0){
						for(let i=0;i<data.resources.length;i++){
							self.ydzq_list.push(data.resources[i]);
						}
						self.nextpage=data.nextPage;
						self.show=false;
						self.sl_flag=false;
					}
					if(data.statusCode==404){
						clearTimeout(timer);
						timer=null;
						self.message='暂无数据';
						self.show=true;
						timer=setTimeout(function(){
							self.show=false;
						}, 2000);
						self.sl_flag=false;
					}
					if(data.status==500){
						clearTimeout(timer);
						timer=null;
						self.message='服务器开小差啦，请稍后再试';
						self.show=true;
						timer=setTimeout(function(){
							self.show=false;
						}, 2000);
						self.sl_flag=false;
					}
				}, self)
				// --------------请求资源列表end--------------
	      		},
	      		_download: function(id, name, loginAccount, token, type){
	      			var path=this.globalurl.host+"/v1/app/res/download?resourceId="+id+"&loginAccount="+loginAccount+"&token="+token;
	      			var fName=name.split(".")[0] ||'';
	      			if(window.js && window.js.downLoadFile){
	      				window.js.downLoadFile(path, fName, type);
	      			}
	      		},
			_goto: function(){
				window.history.back();
			},
			// 点赞
			_dz: function(obj, event){
				var self=this;
				if(obj.isZan==0){
					obj.isZan+=1;
					obj.toast+=1;
					this.util.request(
					{
						url: self.globalurl.host+self.globalurl.url.tsdz,
						data: {"bookId": obj.id, "bookType": self.type, "loginAccount":self.util.getSessionData('userName')},
						dataType: 'json',
						type: 'POST'
					}, function(data){
					}, self)
				}
			},
		},
		filters: {
			src: function(value){
				if(value=="PPT"){
					return '../../static/images/ico_ppt.png';
				}
				if(value=="PDF"){
					return '../../static/images/ico_pdf.png';
				}
				if(value=="DOC"){
					return '../../static/images/ico_doc.png';
				}
				if(value=="MP3"||value=="MP4"){
					return '../../static/images/ico_mp3.png';
				}
			}
		},
		created: function(){
			this.token=this.util.getSessionData('token');
			var self=this;
			var timer=null;
			this.message='加载中';
			this.show=true;
			this.loginAccount=this.util.getSessionData('userName');
			// --------------请求分类begin--------------
			this.util.request(
			{
				url: self.globalurl.host+self.globalurl.url.fllb,
				data: {},
				dataType: 'json',
				type: 'POST'
			}, function(data){
					if(data.statusCode==200&&data.modues.length!=0){
						for(let i=0;i<data.modues.length;i++){
							self.data.push(data.modues[i]);
						}
					}
			}, self)
			// --------------请求分类end--------------

			// --------------请求资源列表begin--------------
			// 默认加载课题科研
			this.flag=7;
			this._load(this.flag);
			// --------------请求资源列表end--------------

			// 上拉加载
			window.addEventListener('scroll', function(){
				if(document.body.scrollTop+document.documentElement.clientHeight==document.body.scrollHeight){
					// 只在阅读教研页面有效
					if(self.$route.path=='/ydjy'){
						if(self.nextpage){
							self.page+=1;
							self.sl_flag=true;
							self._load(self.flag);
						}else{
							clearTimeout(timer);
							timer=null;
							self.show=true;
							self.message='数据已到底';
							self.show=true;
							timer=setTimeout(function(){
								self.show=false;
							}, 2000);
						}
					}
				}
			})

			var type = this.util.getAppScene();
			if(type == 'app'){//app应用端调用
				this.t_app=true;
			}else{
				this.t_app=false;
			}
		},
		components: {
			'tips': tips
		}
	}
</script>
<style scoped>
	a{
		display: block;
	}
	/*头部*/
	header{
		position: fixed;
		z-index: 1;
		top: 0px;
		left: 0px;
		width: 100%;
		height: 0.88rem;
		background-color: #40cc8b;
		text-align: center;
	}
	/*header p{
		font-size: 0.36rem;
		line-height: 0.88rem;
		color: #fff;
	}*/
	header .search{
		width: 75%;
		height:0.7rem; 
		line-height:0.7rem; 
		color: #777777;
		font-size: 0.32rem; 
		border-radius: 0.08rem; 
		background-color: #fff;
		background-image: url('../../static/images/ico_look.png');  
		background-repeat: no-repeat; 
		background-position: 0.15rem;
		background-size: 0.44rem;  
		/*text-indent: 0.7rem; */
		margin-left: 1rem;
		margin-top: 0.08rem;
		border:none;
	}
	.goback{
		position: absolute;
		top: 50%;
		left: 0.25rem;
		margin-top: -0.185rem;
		display: inline-block;
		width: 0.37rem;
		height: 0.37rem;
		background-image: url('../../static/images/pic_fh.png');
		background-size: 100% 100%;
	}
	.nav{
		position: fixed;
		top: 0px;
		z-index: 999999999;
		width: 100%; 
		height: 0.8rem; 
		/*position: relative; */
		margin-top:0.88rem; 
		background-color: #40cc8b; 
		line-height: 0.8rem; 
		display: inline-block;
		background-image: url("../../static/images/pic_more_fff.png");
		background-position: 7.02rem center;
		background-size: 0.2rem 0.3rem;
		background-repeat: no-repeat;
	}
	.navYes{
		 background-color: #2bb575; 
		 padding: 0.1rem 0.25rem; 
		 border-radius: 0.08rem;
	}
	.nav_li{
		width: 93%;
		display: box;
		display: -webkit-box;
		overflow: auto;
	}
	.nav_li li{
		width: 32%;
		 text-align: center; 
		 font-size: 0.32rem; 
		 color: #fff;
	}
	.ydjy_list{
		width: 100%;  
		height: 2.28rem; 
		background-color: #fff; 
		/*position: relative;*/
		/*top:0.85rem; */
		border-bottom: #ececec 1px solid;
	}
	.ydjy_list:nth-child(3){
		margin-top: 1.6rem;
	}
	.ydjy_l{
		width: 2.1rem; 
		height:1.9rem; 
		margin: 0.18rem 0.4rem 0.18rem 0.32rem; 
		float: left; 
		background-color: #f5faf8; 
		text-align: center; 
		vertical-align: middle;
	}
	.ydjy_l img{
		width: 1.12rem;
		height: 1.47rem; 
		margin-top: 0.22rem; 
	}
	.ydjy_r{
		height: 100%; 
		width: 4.55rem; 
		float: left; 
	}
	.headImg{ 
		width:0.6rem; 
		height: 0.6rem;
		margin-right: 0.2rem;
		margin-top: 0.08rem;
	}
	.record{
		width: 100%; 
		height: 0.5rem;
		line-height: 0.5rem; 
		font-size: 0.3rem; 
		display: inline-block; 
		color: #999; 
		font-family: 微软雅黑; 
	}
	.record li{
		float: left;
		width: 25%;
	}
	.pic_see{
		width: 0.33rem; 
		height: 0.22rem; 
	}
	.pic_praise{
		width: 0.28rem; 
		height: 0.23rem; 
	}
	.pic_comment{
		width: 0.26rem; 
		height: 0.27rem; 
	}
	.pic_download{
		width: 0.26rem; 
		height: 0.27rem;
	}
	.hdzq_titil{
		color:#333; 
		font-size:0.36rem; 
		line-height: 0.8rem; 
		overflow:hidden; 
		text-overflow:ellipsis; 
		white-space:nowrap;
	}
	.hdxq_school{
		font-size: 0.3rem; 
		color: #ccc; 
		line-height: 0.4rem;
		overflow:hidden; 
		text-overflow:ellipsis; 
		white-space:nowrap;
	}
	.hdzq_time{
		color:#40cc8b; 
		font-size: 0.32rem; line-height: 0.4rem;
		overflow:hidden; 
		text-overflow:ellipsis; 
		white-space:nowrap;
	}
	.tips{
		float: left;
		text-align: center;
		font-size: 0.33rem;
		color: #666666;
		/*margin: 0.2rem 0px;*/
		background-color: #f3f3ef;
	}
</style>


