<template>
	<div>
		<div class="main">
			<div class="m_t">
				<p>您的账号在其他设备上登录，请重新登录</p>
			</div>
			<button @click='_sure'>确&nbsp;&nbsp;&nbsp;定</button>
		</div>
	</div>
</template>

<script>
	export default {
		methods: {
			_sure: function(){
				var type=this.util.getAppScene();
				if(type=='app'){
					window.js.logout();
				}else{
					WeixinJSBridge.call('closeWindow');
				}
			}
		}
	}
</script>

<style scoped>
	.main{
		position: absolute;
		top: 50%;
		left: 50%;
		width: 6rem;
		height: 3rem;
		border-radius: 0.2rem;
		background-color: #fff;
		overflow: hidden;
		margin-top: -1.5rem;
		margin-left: -3rem;
	}
	.m_t{
		width: 100%;
		height: 2rem;
		border-bottom: 0.01rem solid #e7e7e7;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 0 0.8rem;
	}
	.m_t p{
		font-size: 0.35rem;
		color: #333333;
		line-height: 0.5rem;
		text-align: center;
	}
	button{
		width: 100%;
		height: 1rem;
		background-color: #fff;
		font-size: 0.35rem;
		color: #568eff;
		border: 0px;
	}
</style>


