<template>
	<div>
		<header>
			<a href="javascript:void(0)" @click="_goback" v-if='!t_app'><img src="../../static/images/xs_pic_fh.png" class="goback"></a>
			<router-link to='/fbrw/ts' v-if='t_app'><img src="../../static/images/xs_pic_fh.png" class="goback"></router-link>
			<p>阅读任务</p>
		</header>
		<div class="ydrw_main">
			<div>
				<img src="../../static/images/bj_top.png" class="ydrw_zt" >
				<div class="ydrw_zt_title">
					<div class="ydrw_ztName">{{title}}</div>
					<div class="ydry_ztlist">刘老师|福田实验小学</div>
					<button class="bj" @click='_bj("bt")'></button>
				</div>
			</div>
			<div  class="ydrw_lump">
				<div class="fontsz">
					<span class="title_img" >&nbsp;</span>任务时间：{{b_time}}&nbsp;~&nbsp;{{e_time}}
					<button class="bj" @click='_bj("sj")'></button>
				</div>
			</div>
			<div class="ydrw_lump" v-show='tjy!=""'>
				<div class="fontsz"><span class="title_img" >&nbsp;</span>老师推荐语
					<button class="bj" @click='_bj("tj")'></button></div>
				<div class="lstj">
					<div class="lstj_l">
						<img src="../../static/images/head_03.png" class="lstj_lpic" style="  ">
						<span class="lstj_lname" style="  ">刘老师</span>
					</div>
					<div class="ydrw_lump_comt" >
						<span>{{tjy}}</span>
						<!-- <div class="voice">
							8'<span  class="pic_yy"></span>
						</div> -->
					</div>

				</div>
			</div>
			<div class="ydrw_lump">
					<div class="fontsz"><span class="title_img" >&nbsp;</span>考核心方式： {{khfs}}
					<button class="bj" @click='_bj("kh")'></button></div>
			</div>
			<div class="ydrw_lump" v-show='ydyq!=""'>
				<div class="fontsz" ><span class="title_img" >&nbsp;</span>阅读要求
					<button class="bj" @click='_bj("yd")'></button></div>
				<div class="ydrw_lump_comt ">
					<span>{{ydyq}}</span>
					<!-- <div class="voice">
						8'<span  class="pic_yy"></span>
					</div> -->
				</div>
			</div>
			<div class="fontsz"  style=" margin: 0 0.2rem; margin-bottom: 0.2rem;">
				<span class="title_img" >&nbsp;</span>本期图书:共{{b_list.length}}本
				<a href='javascript:void(0)' class="bj" @click='_goto'></a>
			</div>
			<div v-for='(b, i) in b_list'>
				<div class="bookName">
					<span style=" font-size: 0.3rem;">图书{{i+1}}</span>
					<p>{{b.name}}</p>
					<button @click='_bj("del", i)'></button>
				</div>
				<div class="book_lump">
					<div>
						<img :src="b.pic"  class="book_lump_img" style="position: absolute;"  />
						<!-- <span class="ydjy_state" >完成阅读</span> -->
					</div>
					<div class="book_lump_info">
						<div class="b_list">作者：{{b.author}}/著 </div>
						<div class="b_list">出版社：{{b.press}}</div>
						<div class="b_list">ISBN:{{b.isbn}}</div>
						<div class="ts_type">
							<span class="ts_label">{{b.bookType}}</span>
						</div>
						<ul class="record" >
							<li>{{b.view}}阅读</li>
							<li>{{b.toast}}点赞</li>
						</ul>
					</div>
				</div>
			</div>
			<div style=" position: fixed; bottom: 0;  ">
				<div class="footes">
					<div class="footes_btn " @click='_send'>确认发布</div>
				</div>
			</div>
		</div>

		<div style=" height: 0.98rem; "></div>

		<!-- 弹出框 -->
		<div class="tck" v-show='tck'>
			<!-- 编辑任务名称 -->
			<div class="bj_t" v-show='bt'>
				<p class="c_m_t">任务名称</p>
				<input type="text" name="" placeholder="请输入任务名称" v-model='title'>
				<button class="close" @click='_close'>确定</button>
			</div>
			<!-- 编辑任务时间 -->
			<div class="bj_d" v-show='sj'>
				<p class="c_m_t">任务时间</p>
				<div class="c_b">
					<input id='t_begin' type="text" name="" :data-lcalendar="cb_time" v-binit readonly v-model='b_time'>
					<p>设置开始时间：</p>
				</div>
				<div class="c_b">
					<input id='t_end' type="text" name="" :data-lcalendar="ce_time" v-einit readonly v-model='e_time'>
					<p>设置结束时间：</p>
				</div>
				<button class="close" @click='_close'>确定</button>
			</div>
			<!-- 编辑老师推荐语 -->
			<div class="bj_l" v-show='tj'>
				<div class="c_py">
					<p class="c_m_t" style='border-bottom: 0px;'>老师推荐语(选填)</p>
					<textarea placeholder="您的推荐能帮助学生更好地理解本次任务..." v-model='tjy'></textarea>
					<!-- <button></button> -->
				</div>
				<button class="close" @click='_close'>确定</button>
			</div>
			<!-- 编辑考核方式 -->
			<div class="bj_k" v-show='kh'>
				<div class="c_k">
					<p class="c_m_t">选择考核方式</p>
					<div class="a_mth">
						<div v-for='(a, i) in a_list' :key='a.name'>
							<input :id='"a"+i' class="a_c" type="checkbox" name="a_mth" :value='a.val' v-model='khfs_l' :style='a.val | inSye(khfs_l)'>
							<label :for='"a"+i'>
								<p class="a_name" :style='a.val | pSye(khfs_l)'>{{a.name}}</p>
							</label>
						</div>
					</div>
				</div>
				<button class="close" @click='_close'>确定</button>
			</div>
			<!-- 编辑阅读要求 -->
			<div class="bj_y" v-show='yd'>
				<div class="c_py">
					<p class="c_ti">阅读要求(选填)</p>
					<textarea placeholder="您的推荐能帮助学生更好地理解本次任务..." v-model='ydyq'></textarea>
					<!-- <button></button> -->
				</div>
				<button class="close" @click='_close'>确定</button>
			</div>

			<!-- 删除提示框 -->
			<tips :msg='message' v-show='tip' v-on:del='_del' v-on:no='_close'></tips>
		</div>
		<!-- 消息提示框 -->
		<msgtips :msg='msgtips' v-show='t_flag'></msgtips>
	</div>
</template>

<script>
	var timer=null;
	import tips from './tips.vue'
	import msgtips from './msgtips.vue'
	export default {
		data(){
			return {
				// 删除提示语
				message: '是否删除该本图书',
				// 删除提示框
				tip: false,
				// 消息提示框
				msgtips: '',
				t_flag: false,
				// 弹出框
				tck: false,
				// 标题
				bt: false,
				// 时间
				sj: false,
				// 推荐
				tj: false,
				// 考核方式
				kh: false,
				// 阅读要求
				yd: false,
				// 开始时间
				b_time: '',
				// 结束时间
				e_time: '',
				// 标题内容
				title: '',
				// 推荐语
				tjy: '',
				// 考核方式
				khfs: '',
				// 选中考核方式
				khfs_l: [],
				// 考核方式列表
				a_list: [
					{val: 2,name: '写读后感'},
					{val: 1,name: '测试'}
				],
				// 阅读要求
				ydyq: '',
				// 书本列表
				b_list: [],
				// 删除书本序号
				del_i: -1,
				// 初始化开始时间
				cb_time: '',
				// 初始化结束时间
				ce_time: '',
				t_app: false
			}
		},
		created: function(){
			var type = this.util.getAppScene();
			if(type == 'app'){//app应用端调用
				this.t_app=true;
			}else{
				this.t_app=false;
			}
			var rwInfo=this.util.getSessionData('rwinfo');
			this.b_time=rwInfo.b_time;
			this.e_time=rwInfo.e_time;
			this.khfs_l=rwInfo.khfs.split(',');
			this.title=rwInfo.name;
			this.tjy=rwInfo.tjy;
			this.ydyq=rwInfo.ydyq;
			this.b_list=rwInfo.b_list;
			// 初始化时间范围
			this.cb_time=rwInfo.b_time+",2100-12-31";
			this.ce_time=rwInfo.e_time+",2100-12-31";
		},
		watch: {
			tck: function(){
				var self=this;
				document.addEventListener('touchmove', function(event){
					if(self.tck){
						event.preventDefault();
					}
				}, { passive: false })
			},
			khfs_l: function(){
				this.khfs='';
				for(let i=0;i<this.khfs_l.length;i++){
					if(this.khfs_l[i]==1){
						if(i==this.khfs_l.length-1){
							this.khfs+='评测';
						}else{
							this.khfs+='评测、';
						}
					}
					if(this.khfs_l[i]==2){
						if(i==this.khfs_l.length-1){
							this.khfs+='写读后感';
						}else{
							this.khfs+='写读后感、';
						}
					}
				}
			}
		},
		methods: {
			// 编辑
			_bj: function(value){
				this.tck=true;
				// 修改标题
				if(value=='bt'){
					this.bt=true;
				}
				// 修改时间
				if(value=='sj'){
					this.sj=true;
				}
				// 修改推荐语
				if(value=='tj'){
					this.tj=true;
				}
				// 修改考核方式
				if(value=='kh'){
					this.kh=true;
				}
				// 修改阅读要求
				if(value=='yd'){
					this.yd=true;
				}
				// 删除
				if(value=='del'){
					this.tip=true;
					this.del_i=arguments[1];
				}
			},
			// 关闭弹出框
			_close: function(){
				this.tck=false;
				this.bt=false;
				this.sj=false;
				this.tj=false;
				this.kh=false;
				this.yd=false;
				this.tip=false;
			},
			// 删除图书
			_del: function(){
				var rwInfo=this.util.getSessionData('rwinfo');
				this.b_list.splice(this.del_i, 1);
				rwInfo.bstr.splice(this.del_i, 1);
				rwInfo.b_list=this.b_list;
				this.util.setSessionData('rwinfo', rwInfo);
				this._close();
			},
			// 跳转
			_goto: function(){
				var self=this;
				if(this.b_list.length>=6){
					clearTimeout(timer);
					timer=null;
					this.msgtips='超过添加数量';
					this.t_flag=true;
					timer=setTimeout(function(){
						self.t_flag=false;
					}, 2000);
				}else{
					this.$router.push('/sc');
				}
			},
			// 发布
			_send: function(){
				var self=this;
				var rwInfo=this.util.getSessionData('rwinfo');
				this.util.request(
				{
					url: self.globalurl.host+self.globalurl.url.fbrw,
					data: {
						"planName": rwInfo.name,
						"beginDate": rwInfo.b_time,
						"endDate": rwInfo.e_time,
						"classes": rwInfo.bj,
						"access": rwInfo.khfs,
						"books": rwInfo.bstr.join(),
						"account": self.util.getSessionData("userName"),
						"reason": rwInfo.tjy,
						"voiceReason": '',
						"requirements": rwInfo.ydyq,
						"requirementsVoice": ''
					},
					dataType: 'json',
					type: 'POST'
				}, function(data){
					if(data.statusCode==500){
						clearTimeout(timer);
						timer=null;
						self.msgtips='发送失败';
						self.t_flag=true;
						timer=setTimeout(function(){
							self.t_flag=false;
						}, 2000);
					}
					if(data.statusCode==200){
						clearTimeout(timer);
						timer=null;
						self.msgtips='发送成功';
						self.t_flag=true;
						timer=setTimeout(function(){
							self.t_flag=false;
							self.util.removeSessionData('rwinfo');
							self.$router.push('/');
						}, 2000);
					}
				}, self)
			},
			_goback: function(){
				window.history.back();
			}
		},
		directives: {
			binit: {
				inserted: function(el){
					var calendar = new lCalendar();
					calendar.init({
					    'trigger': '#t_begin',
					    'type': 'date'
					});
				}
			},
			einit: {
				inserted: function(el){
					var calendar = new lCalendar();
					calendar.init({
					    'trigger': '#t_end',
					    'type': 'date'
					});
				}
			},
			ckhfs: {
				inserted: function(el){
					el.onclick=function(){
						if(el.getElementsByTagName('input')[0].checked){
							el.getElementsByTagName('input')[0].style.backgroundImage="url('../../static/images/radio_0ff.png')";
							el.getElementsByTagName('p')[0].style.color='#777777';
						}else{
							el.getElementsByTagName('input')[0].style.backgroundImage="url('../../static/images/Radio_no.png')";
							el.getElementsByTagName('p')[0].style.color='#b7b7b7';
						}
					}
				}
			}
		},
		components: {
			'tips': tips,
			// 消息提示框
			'msgtips': msgtips
		},
		filters: {
			inSye: function(val){
				var flag=false;
				for(let i=0;i<arguments[1].length;i++){
					if(arguments[1][i]==val){
						flag=true;
						break;
					}
				}
				if(flag){
					return {backgroundImage: "url('../../static/images/radio_0ff.png')"}
				}else{
					return {backgroundImage: "url('../../static/images/Radio_no.png')"}
				}
			},
			pSye: function(val){
				var flag=false;
				for(let i=0;i<arguments[1].length;i++){
					if(arguments[1][i]==val){
						flag=true;
						break;
					}
				}
				if(flag){
					return {color: "#777777"}
				}else{
					return {color: "#b7b7b7"}
				}
			}
		},
		// 导航离开钩子
		beforeRouteLeave(to, from, next){
			if(to.path=='/fbrw/ts'){
				var rwInfo=this.util.getSessionData('rwinfo');
				rwInfo.b_time=this.b_time;
				rwInfo.e_time=this.e_time;
				rwInfo.khfs=this.khfs_l.join();
				rwInfo.name=this.title;
				rwInfo.tjy=this.tjy;
				rwInfo.ydyq=this.ydyq;
				this.util.setSessionData('rwinfo', rwInfo);
				next();
			}else{
				next();
			}
		},
		beforeRouteEnter(to, from, next){
			next(function(vm){
				vm.util.removeSessionData('tj_yl');
			})
		}
	}
</script>

