		width: 0.45rem;
		height: 0.65rem;
		background-image: url('../../static/images/ico_02.png');
		background-size: 100% 100%;
	}
	.bj_k{
		position: absolute;
		top: 50%;
		left: 50%;
		width: 7rem;
		padding: 0.2rem 0.3rem;
		background-color: #fff;
		border-radius: 0.05rem;
		margin-top: -2.25rem;
		margin-left: -3.5rem;
	}
	.c_k{
		width: 100%;
	}
	.a_mth{
		overflow: auto;
