		padding-left: 0.1rem;
	}
	.a_name{
		display: inline-block;
		height: 0.54rem;
		line-height: 0.54rem;
		font-size: 0.38rem !important;
		vertical-align: top;
		color: #b7b7b7;
		margin-right: 0.8rem;
	}
	.c_py{
		position: relative;
		width: 100%;
	}
	.c_ti{
		border-left: 0.1rem solid #40cc8b;
		font-size: 0.35rem;
		padding-left: 0.1rem;
	}
	.c_py textarea{
		margin-top: 0.3rem;
		width: 100%;
		resize: none;
		border-radius: 0.1rem;
		height: 3rem;
		border: 0.01rem solid #e7e7e7;
		padding: 0.2rem;
		font-size: 0.33rem;
	}
	.c_py button{
		border: 0px;
		background-color: rgba(0,0,0,0);
		position: absolute;
		bottom: 0.2rem;
		right: 0.15rem;
		width: 0.45rem;
		height: 0.65rem;
		background-image: url('../../static/images/ico_02.png');
		background-size: 100% 100%;
	}
	/*编辑按钮*/
	.bj{
		display: block;
		position: absolute;
		top: 0rem;
		right: 0rem;
