<template>
	<div style=" background-color: #fafafa; ">
		<header>
			<p>
				<img src="../../static/images/ico_look.png" class="look">
				<input type="text" name="" placeholder="搜索资源" v-model='value'>
			</p>
			<router-link to='/ydjy' v-if='t_app'><span class="goback_r">取消</span></router-link>
			<a href="javascript:void(0)" @click='_goback' v-if='!t_app'><span class="goback_r">取消</span></a>
		</header>
		<div class="main" v-show='flag'>
			<div class="hot_list">
				<img src="../../static/images/pic_hot.png" class="hot">
				<span>热门搜索</span>
			</div>
			<div class="hot_list" :key='i' v-for='(h, i) in hotList' @click='_selectHot(h.word)'>
				<span class="hot orange">{{i+1}}</span>
				<span>{{h.word}}</span>
			</div>
		</div>
		<!-- 书详情内容 -->
		<div class="ydjy_list" v-for="(ydzq, i) in ydzq_list" :key='ydzq.id'>
			<div class="ydjy_l"><img :src="ydzq.type | src"></div>
			<div class="ydjy_r">
				<div class="hdzq_titil" >{{ydzq.name}}</div>
				<dl>
					<dt style=" float: left; ">
						<img :src="ydzq.avatar" class="headImg"></img>
					</dt>
					<p class="hdzq_time">{{ydzq.author}}</p>
					<p class="hdxq_school">{{ydzq.schoolName}}</p>
				</dl>
				<ul class="record">
					<li><img src="../../static/images/ico_heart.png" class="pic_praise"></img >&nbsp;{{ydzq.love}}</li>
					<li>
						<form action='/v1/app/res/download' method="GET" v-if='!t_app'>
							<input type="hidden" name="resourceId" :value='ydzq.id'>
							<input type="hidden" name="loginAccount" :value='loginAccount'>
							<input type="hidden" name="token" :value='token'>
							<input type="submit" name="" :id="'sub'+i" style='display: none;'>
							<label :for="'sub'+i" style='color: #999;'>
								<img src="../../static/images/ico-down.png" class="pic_download"></img>&nbsp;{{ydzq.download}}
							</label>
						</form>
						<div v-if='t_app' @click='_download(ydzq.id, ydzq.name, loginAccount, token)'>
							<img src="../../static/images/ico-down.png" class="pic_download"></img>&nbsp;{{ydzq.download}}
						</div>
					</li>
				</ul>
			</div>
		</div>
		<tips :msg='message' v-show='t_flag'></tips>
	</div>
</template>

<script>
	var timer=null;
	import tips from './msgtips.vue'
	export default {
		data(){
			return {
				// 热榜
				flag: true,
				// 阅读教研 列表
				ydzq_list: [],
				// 搜索框
				value: '',
				// 页码
				page: 1,
				// 上拉下载
				sl_flag: false,
				// 提示框
				message: '',
				t_flag: false,
				t_app: false,
				loginAccount: '',
				hotList: []
			}
		},
		created: function(){
			this.token=this.util.getSessionData('token');
			this.loginAccount=this.util.getSessionData('userName');
			var self=this;
			// 热门关键字
			this.util.request(
			{
				url: self.globalurl.host+self.globalurl.url.tsrm,
				data: {},
				dataType: 'json',
				type: 'POST'
			}, function(data){
				if(data.status==200){
					self.hotList=data.result;
				}
			}, self)
			// 上拉加载
			window.addEventListener('scroll', function(){
				if(document.body.scrollTop+document.documentElement.clientHeight==document.body.scrollHeight){
					// 只在阅读教研页面有效
					if(self.$route.path=='/ydjy/ss'){
						self.page+=1;
						self.sl_flag=true;
						self._load();
					}
				}
			})
			var type = this.util.getAppScene();
			if(type == 'app'){//app应用端调用
				this.t_app=true;
			}else{
				this.t_app=false;
			}
		},
		filters: {
			src: function(value){
				if(value=="PPT"){
					return '../../static/images/ico_ppt.png';
				}
				if(value=="PDF"){
					return '../../static/images/ico_pdf.png';
				}
				if(value=="DOC"){
					return '../../static/images/ico_doc.png';
				}
				if(value=="MP3"||value=="MP4"){
					return '../../static/images/ico_mp3.png';
				}
			}
		},
		watch: {
			value: function(val){
				if(val!=false){
					this._load();
				}
			}
		},
		methods: {
			_load: function(){
				var self=this;
	           		if(!this.sl_flag){
	           			this.ydzq_list=[];
	           			this.page=1;
				}
				this.message='加载中';
				this.t_flag=true;
				// --------------请求资源列表begin--------------
				this.util.request(
				{
					url: self.globalurl.host+self.globalurl.url.zyxqss,
					data: {"keywords":self.value,"page":self.page,"pageSize": "10"},
					dataType: 'json',
					type: 'POST'
				}, function(data){
					if(data.statusCode==200&&data.resources.length!=0){
						if(self.value!=false){
							for(let i=0;i<data.resources.length;i++){
								self.ydzq_list.push(data.resources[i]);
							}
							self.t_flag=false;
							self.sl_flag=false;
							self.flag=false;
						}else{
							self.t_flag=false;
							self.flag=true;
							self.ydzq_list=[];
						}
					}
					if(data.statusCode==404){
						clearTimeout(timer);
						timer=null;
						self.message='抱歉，没有相关资料';
						self.t_flag=true;
						timer=setTimeout(function(){
							self.t_flag=false;
						}, 2000);
						self.sl_flag=false;
						self.flag=true;
					}
					if(data.status==500){
						clearTimeout(timer);
						timer=null;
						self.message='服务器开小差啦，请稍后再试';
						self.t_flag=true;
						timer=setTimeout(function(){
							self.t_flag=false;
						}, 2000);
						self.sl_flag=false;
						self.flag=true;
					}
				}, self)
				// --------------请求资源列表end--------------
			},
			_goback: function(){
				window.history.back();
			},
			_selectHot: function(val){
				this.value=val;
			},
	      		_download: function(id, name, loginAccount, token){
	      			var path=this.globalurl.host+"/v1/app/res/download?resourceId="+id+"&loginAccount="+loginAccount+"&token="+token;
	      			var fName=name.split(".")[0];
	      			var fGs=name.split(".")[1];
	      			if(window.js && window.js.downLoadFile){
	      				window.js.downLoadFile(path, fName, fGs);
	      			}
	      		}
		},
		components: {
			'tips': tips
		}
	}
</script>

<style scoped>
	header{
		height: 0.98rem;
		background-color: #fff;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	header p{
		display: flex;
		align-items: center;
		font-size: 0.36rem;
		line-height: 0.88rem;
		width: 80%;
		background-color: #f0f0f0;
		border-radius: 0.08rem; 
		margin: 0.12rem 0.2rem;

	}
	header p input{
		width: 83%;
		height:0.7rem; 
		font-size: 0.32rem; 
		background-color: #f0f0f0;
		border:none;
	}
	.look{
		width: 0.44rem; 
		height: 0.44rem; 
		margin: 0 0.2rem; 
	}
	.main{
		position: relative; 
		top: 1.2rem; 
		background-color: #fff; 
		padding-bottom: 0.3rem;
	}
	.goback_r{
		position: absolute;
		top: 50%;
		right: 0.25rem;
		margin-top: -0.185rem;
		display: inline-block;
		width: 1rem;
		height: 0.37rem; 
		font-size: 0.32rem;
		color: #666;
	}
	
	.hot_list{
		display: flex; 
		justify-content: left; 
		align-items: center; 
		border-bottom: #e7e7e7 0.01rem solid;
		height: 0.88rem;
		font-size: 0.3rem;
		margin-left: 0.3rem;
		color: #666;
	}
	.hot{
		width: 0.4rem;
		height: 0.4rem;
		margin: 0 0.2rem 0 0;
	}
	.orange{
		color:#f60;
	}
	.ydjy_list:nth-child(3){
		margin-top: 1rem;
	}
	.ydjy_list{
		width: 100%;  
		height: 2.28rem; 
		background-color: #fff; 
		/*position: relative;*/
		/*top:0.85rem; */
		border-bottom: #ececec 1px solid;
	}
	.ydjy_l{
		width: 2.1rem; 
		height:1.9rem; 
		margin: 0.18rem 0.4rem 0.18rem 0.32rem; 
		float: left; 
		background-color: #f5faf8; 
		text-align: center; 
		vertical-align: middle;
	}
	.ydjy_l img{
		width: 1.12rem;
		height: 1.47rem; 
		margin-top: 0.22rem; 
	}
	.ydjy_r{
		height: 100%; 
		width: 4.55rem; 
		float: left; 
	}
	.headImg{ 
		width:0.6rem; 
		height: 0.6rem;
		margin-right: 0.2rem;
		margin-top: 0.08rem;
	}
	.record{
		width: 100%; 
		height: 0.5rem;
		line-height: 0.5rem; 
		font-size: 0.3rem; 
		display: inline-block; 
		color: #999; 
		font-family: 微软雅黑; 
	}
	.record li{
		float: left;
		width: 25%;
	}
	.pic_see{
		width: 0.33rem; 
		height: 0.22rem; 
	}
	.pic_praise{
		width: 0.28rem; 
		height: 0.23rem; 
	}
	.pic_comment{
		width: 0.26rem; 
		height: 0.27rem; 
	}
	.pic_download{
		width: 0.26rem; 
		height: 0.27rem;
	}
	.hdzq_titil{
		color:#333; 
		font-size:0.36rem; 
		line-height: 0.8rem; 
		overflow:hidden; 
		text-overflow:ellipsis; 
		white-space:nowrap;
	}
	.hdxq_school{
		font-size: 0.3rem; 
		color: #ccc; 
		line-height: 0.4rem;
		overflow:hidden; 
		text-overflow:ellipsis; 
		white-space:nowrap;
	}
	.hdzq_time{
		color:#40cc8b; 
		font-size: 0.32rem; line-height: 0.4rem;
		overflow:hidden; 
		text-overflow:ellipsis; 
		white-space:nowrap;
	}
	.tips{
		float: left;
		text-align: center;
		font-size: 0.33rem;
		color: #666666;
		/*margin: 0.2rem 0px;*/
		background-color: #f3f3ef;
	}

</style>


