<template>
	<div id="ydzx" style=" background-color: #fff; font-family: 黑体; ">
		<header>
			<a href="javascript:void(0)"  class="goback" @click="_goto" v-if='!t_app'></a>
			<router-link to='/' class='goback' v-if='t_app'></router-link>
			<p>阅读资讯</p>
		</header>
		<div class="ydzx_main">
			<div class="ydzx_l">
				<div class="ydzx_titil" >{{info.title}}</div>
				<div class="ydzx_time">{{info.result}}<span style="float: right; color:#40cc8b; ">一起阅读平台</span></div>
			</div>
			<div class="ydzx_text" v-html='info.content'></div>
		</div>
		<tips :msg='message' v-show='flag'></tips>
	</div>
</template>
<script>
	import tips from './msgtips.vue'
	export default {
		data(){
			return {
				// 活动列表
				info: {},
				// 提示框
				message: '',
				flag: false,
				t_app: false
			}
		},
		name: 'ydzx_xq',
		created: function(){
			var type = this.util.getAppScene();
			if(type == 'app'){//app应用端调用
				this.t_app=true;
			}else{
				this.t_app=false;
			}
			var self=this;
			this.message='加载中';
			this.flag=true;
			this.util.request(
			{
				url: self.globalurl.host + self.globalurl.url.zxxq + self.$route.query.id,
				data: {},
				dataType: 'json',
				type: 'POST'
			}, function(data){
				if(data.status==200&&data.result!=null){
						self.info=data.result;
						self.flag=false;
				}
			}, self)
		},
		components: {
			"tips": tips
		},
		methods: {
			_goto: function(){
				window.history.back();
			}
		}
	}
</script>
<style scoped >
	/*头部*/
	header{
		position: fixed;
		z-index: 1;
		top: 0px;
		left: 0px;
		width: 100%;
		height: 0.88rem;
		background-color: #40cc8b;
		text-align: center;
	}
	header p{
		font-size: 0.36rem;
		line-height: 0.88rem;
		color: #fff;
	}
	.goback{
		position: absolute;
		top: 50%;
		left: 0.25rem;
		margin-top: -0.185rem;
		display: inline-block;
		width: 0.37rem;
		height: 0.37rem;
		background-image: url('../../static/images/pic_fh.png');
		background-size: 100% 100%;
	}
	.ydzx_main{
		width: 100%; 
		position: relative;
		top:0.88rem;
	}
	.ydzx_l{
		padding: 0.2rem 0.3rem;
	}
	.ydzx_img{
		display: inline-block;
		width: 6.9rem;
		height: 3.6rem;
		margin: 0 0.3rem;
		background-size: 100% 100%;
	}
	.ydzx_text{
		font-size: 0.32rem;
		color: #666;
		padding: 0.3rem;
		line-height: 0.45rem; 
		margin-bottom: 1.2rem;
		background-color: #fff;
		text-align:justify;
	}
	.ydzx_titil{
		color:#333; 
		font-size:0.40rem; 
		line-height: 0.8rem; 
		text-align: center;
	}
	.ydzx_time{
		color:#b7b7b7; 
		font-size: 0.32rem;
		line-height: 0.6rem;
	}
</style>


