<template>
	<div id="sc" style=" background-color: #fff; ">
		<header>
			<router-link :to='"/sk/sk_search?path="+tj_yl' ><img class="goback" src="../../static/images/pic_ss.png"></router-link>
			<p>书库</p>
			<img class="goback_r" src="../../static/images/ico_18.png" @click='_sm'>
		</header>
		<div class="nav">
			<ul  class="nav_li" >
				<li v-for='(item,index) in data' @click='_light(index, item.val)'><span v-bind:class="{'navYes':flag==index}">{{item.name}}</span></li>
			</ul>
		</div>
		<ul class="nav1">
			<li style="position:relative;border-right: #e6e6e6 solid 0.01rem;background-color: #fff;">
				<p class="nav_t">年级：</p>
				<select class="s_class" v-model='nj_c'>
					<option selected value=''>全部</option>
					<option v-for='nj in nj_list' :key='nj.id' :value='nj.name'>{{nj.name}}</option>
				</select>
			</li>
			<li style="position:relative;background-color: #fff;">
				<p class="nav_t">类型：</p>
				<select class="s_class" v-model='lb_c'>
					<option selected value=''>全部</option>
					<option v-for='lb in lb_list' :key='lb.id' :value='lb.id'>{{lb.name}}</option>
				</select>
			</li>
		</ul>
		<foot :show="1"></foot>
		<div class="qx" v-show='qx_flag'>
			<a href='javascript:void(0)' @click='_goto'>取&nbsp;&nbsp;消</a>
		</div>
		<tips :msg='message' v-show='t_flag'></tips>
		<div class="ydjy_list" v-for="sc in sc_list" :key='sc.id'>
			<router-link :to="'/sc/xq?id='+sc.id+'&type='+type" >
			<div class="ydjy_l">
				<img :src="sc.pic" class="ydjy_lPic" style=" position: relative; ">
			</div>
	 		</router-link>
			<div class="ydjy_r">
				<router-link :to="'/sc/xq?id='+sc.id+'&type='+type">
				<div class="hdzq_titil" >{{sc.name}}</div>
				<div class="author">{{sc.author}}<span>&nbsp;&nbsp;&nbsp;著</span></div>
				<div style="display: flex; justify-content: left; align-items: center;">
						<span class="ts_label">{{sc.bookType}}</span>
				</div></router-link>
				<ul class="record">
					<li><img src="../../static/images/ico_see.png" class="pic_see" ></img>&nbsp;{{sc.view}}</li>
					<li @click='_dz(sc, $event)'><img :src="sc.isZan | dzImg" class="pic_praise"></img >&nbsp;{{sc.toast}}</li>
				</ul>
				<div class="btn">
					<a href="javascript:void(0)" @click='_addTs(sc)'></a>
					<a href="javascript:void(0)" v-if='sc.haveQuestion==1' @click='_tips'></a>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	var sCode=null;
	var timer=null;
	import tips from './msgtips.vue'
	import f from './footer.vue'
	export default {
		data(){
			return {
				// 提示框
				message: '',
				t_flag: false,
				// 底部取消按钮
				qx_flag: false,
				// 年级列表
				nj_list: [],
				// 选中年级
				nj_c: '',
				// 类别列表
				lb_list: [],
				// 选中类别
				lb_c: '',
				// 阅读教研 列表
				sc_list: [],
				data:[
					{val:1,name:'书城'},
					{val:1,name:'专家推荐'},
					{val:2,name:'老师推荐'}
				],
				// 类别
				type: 1,
				flag:0,
				// 是否从添加图书(true)来还是预览(false)
				tj_yl: true,
				// 页码
				page: 1,
				// 上拉加载
				sl_flag: false,
				// 学校id
				schoolCode: '',
				// 点赞
				dzImg: '../../static/images/ico_heart.png',
				nextpage: true,
				t_app: false
			}
		},
		name: 'sc',
		filters: {
			dzImg: function(val){
				if(val==0){
					return '../../static/images/ico_heart.png';
				}else{
					return '../../static/images/ico_heart_show.png';
				}
			}
		},
		methods: {
			// 扫描
			_sm: function(){
				var self=this;
				// alert(2);
				if(!this.t_app){
					wx.scanQRCode({
						needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
						scanType: ["qrCode","barCode"], // 可以指定扫二维码还是一维码，默认二者都有
						success: function (res) {
							var result = res.resultStr; // 当needResult 为 1 时，扫码返回的结果
							// alert(result);
							self.$router.push('/sk/sk_search?isbn='+result);
						}
					});
				}else{
					if(window.js && window.js.scan){
						window.js.scan();
					}
				}
			},
			// 点赞
			_dz: function(sc, event){
				var self=this;
				if(sc.isZan==0){
					sc.isZan+=1;
					sc.toast+=1;
					this.util.request(
					{
						url: self.globalurl.host+self.globalurl.url.tsdz,
						data: {"bookId": sc.id, "bookType": self.type, "loginAccount":self.util.getSessionData('userName')},
						dataType: 'json',
						type: 'POST'
					}, function(data){
					}, self)
				}
			},
			// 提示
			_tips: function(){
				var self= this;
				this.message='题库模块等待上线中';
				this.t_flag=true;
				timer=setTimeout(function(){
					self.t_flag=false;
				}, 2000);
			},
			_light:function(i, type){
		            	this.flag=i;
		            	this.type=type;
		            	// 加载数据
		            	this._load();
			},
			_addTs: function(ts){
				var self=this;
				if(ts.haveQuestion==1){
					// 当前有数据保存在DOM存储里(从发布任务页面跳过来)
					if(this.util.getSessionData('rwinfo')){
						var rwInfo=this.util.getSessionData('rwinfo');
						if(rwInfo.b_list&&rwInfo.bstr){
							// 重复则什么都不做
							for(let i=0;i<rwInfo.b_list.length;i++){
								if(rwInfo.b_list[i].id==ts.id){
									clearTimeout(timer);
									timer=null;
									this.message='已添加';
									this.t_flag=true;
									timer=setTimeout(function(){
										self.t_flag=false;
									}, 2000);
									return false;
								}
							}
							rwInfo.b_list.push(ts);
							rwInfo.bstr.push(ts.id+"+"+self.type);
						}else{
							rwInfo.b_list=[];
							rwInfo.bstr=[];
							rwInfo.b_list.push(ts);
							rwInfo.bstr.push(ts.id+"+"+self.type);
						}
						this.util.setSessionData('rwinfo', rwInfo);
						this._goto();
					}else{
						// 当前没有数据保存在DOM存储里
						var rwInfo={};
						rwInfo.b_list=[];
						rwInfo.bstr=[];
						rwInfo.b_list.push(ts);
						this.util.setSessionData('rwinfo', rwInfo);
						rwInfo.bstr.push(ts.id+"+"+self.type);
						this.$router.push("/fbrw");
					}
				}else{
					clearTimeout(timer);
					timer=null;
					this.message='该书暂无题库';
					this.t_flag=true;
					timer=setTimeout(function(){
						self.t_flag=false;
					}, 2000);
					return false;
				}
			},
			_goto: function(){
				if(this.t_app){
					// 判断页面是从添加图书跳转还是预览跳转
					if(this.tj_yl){
						this.$router.push("/fbrw/ts");
					}else{
						this.$router.push("/fbrw/yl");
					}
				}else{
					window.history.back();
				}
			},
			_load: function(){
				var self=this;
				if(!this.sl_flag){
					this.sc_list=[];
					this.page=1;
				}
				if(this.type==2){
					this.schoolCode=sCode;
				}else{
					this.schoolCode='';
				}
				this.message='加载中';
				this.t_flag=true;
				// --------------请求图书列表begin--------------
				this.util.request(
				{
					url: self.globalurl.host+self.globalurl.url.tslb,
					data: {"moduleId": self.lb_c, "grade": self.nj_c, "type": self.type, "schoolCode": self.schoolCode, "page": self.page, "pageSize": 10,"loginAccount":self.util.getSessionData('userName')},
					dataType: 'json',
					type: 'POST'
				}, function(data){
					if(data.statusCode==200&&data.books.length!=0){
						for(let i=0;i<data.books.length;i++){
							self.sc_list.push(data.books[i]);
						}
						self.nextpage=data.nextPage;
						self.sl_flag=false;
						self.t_flag=false;
					}
					if(data.statusCode==404){
						clearTimeout(timer);
						timer=null;
						self.message='暂无数据';
						timer=setTimeout(function(){
							self.t_flag=false;
						}, 2000);
						self.sl_flag=false;
					}
					if(data.statusCode==500){
						clearTimeout(timer);
						timer=null;
						self.message='服务器开小差啦，请稍后再试';
						self.t_flag=true;
						timer=setTimeout(function(){
							self.t_flag=false;
						}, 2000);
						self.sl_flag=false;
					}
				}, self)
				// --------------请求图书列表end--------------
			}
		},
		components: {
			'foot': f, // 添加底部导航组件,
			'tips': tips
		},
		created: function(){
			// 判断是不是从发布任务来
			if(this.util.getSessionData('tj_yl')){
				this.qx_flag=true;
				if(this.util.getSessionData('tj_yl')==1){
					// 添加图书
					this.tj_yl=true;
				}else{
					// 预览
					this.tj_yl=false;
				}
			}
			var self=this;
			// --------------请求年级列表begin--------------
			this.util.request(
				{
					url: self.globalurl.host+self.globalurl.url.sknj,
					data: {},
					dataType: 'json',
					type: 'POST'
				}, function(data){
					if(data.statusCode==200&&data.grade.length!=0){
						self.nj_list=data.grade;
					}
				}, self)
			// --------------请求年级列表end--------------

			// --------------请求类别列表begin--------------
			this.util.request(
				{
					url: self.globalurl.host+self.globalurl.url.sklb,
					data: {},
					dataType: 'json',
					type: 'POST'
				}, function(data){
					if(data.statusCode==200&&data.modules.length!=0){
						self.lb_list=data.modules;
					}
				}, self)
			// --------------请求年级列表end--------------

			// --------------请求学校Codebegin--------------
			sCode=self.util.getSessionData('schoolCode');
			// --------------请求学校id列表end--------------

			// 加载图书
			this._load();

			// 上拉加载
			window.addEventListener('scroll', function(){
				if(document.body.scrollTop+document.documentElement.clientHeight==document.body.scrollHeight){
					if(self.$route.path=='/sc'){
						if(self.nextpage){
							self.page+=1;
							self.sl_flag=true;
							self._load();
						}else{
							clearTimeout(timer);
							timer=null;
							self.message='数据已到底';
							self.t_flag=true;
							timer=setTimeout(function(){
								self.t_flag=false;
							}, 2000);
						}
					}
				}
			})

			// 微信SDK
			var ret=this.util.getSessionData('rets');
			wx.config({
				debug: false,
				appId: ret.data.appID,
				timestamp: ret.data.timestamp,
				nonceStr: ret.data.nonceStr,
				signature: ret.data.signature,
				jsApiList: [
					'scanQRCode'
				]
			});

			window.scanResult=function(result){
				self.$router.push('/sk/sk_search?isbn='+result);
			};

			var type = this.util.getAppScene();
			if(type == 'app'){//app应用端调用
				this.t_app=true;
			}else{
				this.t_app=false;
			}
		},
		watch: {
			nj_c: function(){
				this._load();
			},
			lb_c: function(){
				this._load();
			}
		},
		beforeRouteEnter(to, from, next){
			if(from.path=='/fbrw/ts'){
				// 如果从发布任务跳转过来 则显示底部取消按钮
				next(function(vm){
					vm.qx_flag=true;
					vm.tj_yl=true;
					vm.util.setSessionData('tj_yl', 1);
				});
			}else if(from.path=='/fbrw/yl'){
				// 如果从发布任务跳转过来 则显示底部取消按钮
				next(function(vm){
					vm.qx_flag=true;
					vm.tj_yl=false;
					vm.util.setSessionData('tj_yl', 2);
				});
			}else{
				next();
			}
		},
		beforeRouteLeave(to, from, next){
			if(!this.t_app){
				if(to.path=="/"){
					window.history.replaceState({title: 'index', url: '#/'}, "index", "#/");
				}else if(to.path=="/main"){
					window.history.replaceState({title: 'main', url: '#/main'}, "main", "#/main");
				}
			}
			next();
		}
	}
</script>
